import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';

const YEAR_RANGE = 'tlInputTeamYear'

@Component({
  selector: 'planning-layout-one',
  templateUrl: './planning-layout-one.component.html',
  styleUrls: ['./planning-layout-one.component.styl']
})
export class PlanningLayoutOneComponent implements OnInit {
  @Input() sectionName = '';
  @Input() chartRangeSuffix= '';
  @Input() isDisable:Boolean= false;
  

  commonChartOptions = {
    xAxis:{
      categories: ['P1', 'P2', 'P3', 'P4']
    },
    yAxis: {
      title: {
        text: ''
      },
      labels: { format: '{value:0}' },
      stackLabels: {
        enabled: true,
        format: '{y:0}',
        style: {
          // fontWeight: 'bold',
          display: 'none'
        }
      }
    },
    chartOptions:{
      title: {
        text: ''
      },
      chart: { 'height': 200, 'width': 200 },
      legend: {
        enabled: false,
        y: 0,
        reversed: false
      },
      plotOptions:
      {
        column:
        {
          dataLabels: {
            enabled: false,
            format: '{y:0}',
            crop: false,
            overflow: true,
            inside: false
          },
        }
      },
      tooltip: {
        pointFormat: '{series.name}:{point.y: 0}'
      }
    }
  }

  constructor(
    private textEngineService: TextHelperService,
    private calcservice: CalcService,
    private elRef: ElementRef,
    private utils: Utils,
    private communicator: CommunicatorService
  ) { }

  ngOnInit() {
    this.chartRangeSuffix = (this.chartRangeSuffix != '') ? this.chartRangeSuffix : this.sectionName;
  }

}
