import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-distribution-section',
  templateUrl: './distribution-section.component.html',
  styleUrls: ['./distribution-section.component.styl']
})
export class DistributionSectionComponent implements OnInit {
  @Input() productNumber = 1
  @Input() regionNumber = 1
  @Input() nameKey;
  @Input() isReversed = false

  constructor() { }

  ngOnInit() {
    if(!this.nameKey){
      this.nameKey = 'Prod'+this.productNumber+'Name'
    }
  }

}
