import { EventEmitter, Injectable } from '@angular/core';
import { Router, UrlTree } from '@angular/router';
import { Dictionary } from './dictionary';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from 'src/app/app-constants';
@Injectable()
export class Utils {
    public _defaultModel: any;
    modelLoadingPromise: any;

    focusElement: any;
    public get getFocusElement(): any {
        return this.focusElement;
    }
    public set setFocusElement(v: any) {
        this.focusElement = v;
    }

    roleFocus: any;
    public get getRoleFocus(): any {
        return this.roleFocus;
    }
    public set setRoleFocus(v: any) {
        if (this.roleFocus != v) {
            this.roleFocus = v;
            this.communicator.trigger(AppConstants.ACTIVE_ROLE, this.roleFocus)
        }
    }

    userObj: any;
    public get getUserObj(): any {
        return this.userObj;
    }
    public set setUserObj(v: any) {
        this.userObj = v;
    }

    constructor(
        private router: Router,
        public calcService: CalcService,
        private communicator: CommunicatorService
    ) { }

    dictToJson(dict) {
        let jsonObj = {}
        dict.forEach((key, value) => {
            let temp = {}
            Object.entries(value.table).forEach(([key, value]) => {
                temp[value['key']] = value['value'];
            }
            );
            jsonObj[value.getValue('ID')] = temp;
        })

        return jsonObj;
    }

    stripOutletFromUrl(url: any, outletName: string): string {
        let tree: UrlTree = (typeof url === "string") ? this.router.parseUrl(url) : url,
            treeFragment = tree.fragment,
            newUrl = '/';

        this.deleteDeepChildOutlet(tree.root, outletName);
        return this.router.serializeUrl(tree);
    }

    deleteDeepChildOutlet(urlSegment: any, outletName: string) {
        let children = urlSegment.children,
            childOutlets = Object.keys(children),
            isRouteFoundDeleted = false;

        if (childOutlets && childOutlets.length) {
            childOutlets.forEach((childOutlet) => {
                if (childOutlet === outletName) {
                    delete children[childOutlet];
                    isRouteFoundDeleted = true;
                    return false;
                }
            })
            if (isRouteFoundDeleted === false) {
                // process children
                childOutlets.forEach((childOutlet) => {
                    isRouteFoundDeleted = this.deleteDeepChildOutlet(children[childOutlet], outletName);
                    if (isRouteFoundDeleted) {
                        return false;
                    }
                })
            }
        }
        return isRouteFoundDeleted;
    }

    stripChildRouteFromUrl(url: string, childRouteName: string): string {
        let tree: UrlTree = this.router.parseUrl(url),
            treeFragment = tree.fragment,
            newUrl = '/',
            foundMatch: boolean = false;

        this.deleteDeepChildRoute(tree.root, childRouteName);
        return this.router.serializeUrl(tree);

    }

    deleteDeepChildRoute(urlSegments: any, childRouteName: string) {
        let segments = urlSegments.segments, index = 0, isSegmentFound = false;

        // check each segment path first
        // then check each childoutlet and their segment paths
        segments.forEach((segment) => {
            if (segment.path === childRouteName) {
                segments.splice(index, 1);
                isSegmentFound = true;

                return false;
            }
            ++index;
        })
        if (!isSegmentFound) {
            let children = urlSegments.children,
                childOutlets = Object.keys(children);

            childOutlets.forEach((outlet) => {
                isSegmentFound = this.deleteDeepChildRoute(children[outlet], childRouteName);
                if (isSegmentFound) {
                    // delete the outlet if it does not have any children or segments left
                    if (children[outlet].segments.length === 0 && children[outlet].numberOfChildren === 0) {
                        delete children[outlet];
                    }
                    return false;
                }
            });

        }
        return isSegmentFound;
    }

    generateFeedbackUrl(id: string) {
        let url = [AppConstants.DASHBOARD_PATH + "/", { outlets: { 'secondary-nav': "feedback/" + id } }];
        return url;
    }

    getCharFromCode(code: number, base: number): string {
        if (base) {
            code += base;
        }
        return String.fromCharCode(code);
    }


    isOutletRouteActive(outletName: string, url: string): boolean {
        let urlTree = this.router.parseUrl(url),
            treeFragment = urlTree.fragment,
            rootChildren = (urlTree.root && urlTree.root.children) ? urlTree.root.children : null;
        // check if routeroutlet is present in rootchildren list
        if (rootChildren && rootChildren[AppConstants.PRIMARY_OUTLET]
            && rootChildren[AppConstants.PRIMARY_OUTLET].children
            && rootChildren[AppConstants.PRIMARY_OUTLET].children[outletName] !== undefined)
            return true;

        return false;
    }

    isRouteActive(route, url): Boolean {
        if (url.indexOf(route) == -1) {
            return false;
        } else {
            return true;
        }
    }

    getPristineModel(): Promise<CalcService> {
        if (this._defaultModel) {
            return Promise.resolve(this._defaultModel);
        } else if (this.modelLoadingPromise === undefined) {
            return this.modelLoadingPromise = this.getCalcService();
        } else {
            return this.modelLoadingPromise;
        }
    }

    getCalcService() {
        return this.calcService.buildModel().then((calcApi: any) => {
            this._defaultModel = calcApi;
            return calcApi;
        }).catch((err) => {
            console.log('err in utils getCalcService', err);
        });
    }

    forceResize() {
        setTimeout(() => {
            if (typeof (Event) === 'function') {
                // modern browsers
                window.dispatchEvent(new Event('resize'));
            } else {
                // for IE and other old browsers
                // causes deprecation warning on modern browsers
                //   var evt = window.document.createEvent('UIEvents'); 
                //   evt.initUIEvent('resize', true, false, window, 0); 
                //   window.dispatchEvent(evt);
            }
        }, 1000);
    }
}

export { Dictionary } from './dictionary';
