import { Component, OnInit, ChangeDetectorRef, ViewChildren, QueryList, ElementRef, ViewChild, HostListener } from '@angular/core';
import { TextService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Router } from '@angular/router';
import { Utils } from 'src/app/shared/utils/utils';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';

@Component({
  selector: 'app-roles-map',
  templateUrl: './roles-map.component.html',
  styleUrls: ['./roles-map.component.styl']
})
export class RolesMapComponent implements OnInit {

  constructor(
    private textService: TextService,
    private textHelperService: TextHelperService,
    private router: Router,
    private utils: Utils,
    private cdRef: ChangeDetectorRef,
    private communicator: CommunicatorService
  ) { }

  roleData: any;
  roleActive = [];
  activeIndex = 0;
  previousIndex = 0;
  svgPath='';
  hideCircle = false;

  activeRoleSubscriber: Subscription

  @ViewChildren('roleImage') roleImagesRef: QueryList<ElementRef>;
  @ViewChild('svgPathRef', {static: false}) svgPathRef: ElementRef;
  @ViewChild('svgCircle', {static: false}) svgCircleRef: ElementRef;

  @HostListener('window:resize', ['$event'])
  public handleWindowResize(event) {
    this.createSVG()
    // this.animateCircle()
  }

  ngOnInit() {
    this.getData();

    this.activeRoleSubscriber = this.communicator.getEmitter(AppConstants.ACTIVE_ROLE).subscribe((val: string)=>{
      // all inactive - null
      // x active - x
      // multiple active - 1, 3, 5
      if(val){
        this.hideCircle = false;
        let indexes = val.split(",")
        this.updateActiveRoles(indexes)
      }else{
        this.roleActive.fill(false);
        this.hideCircle = true;
      }
    })
  }

  ngAfterViewInit() {
    this.createSVG()
    // this.moveCircle(this.activeIndex)
    // this.animateCircle()
  }

  createSVG(){
    this.svgPath = '';
    let roleImagesArray = this.roleImagesRef.toArray();
    for (let i = 0; i < roleImagesArray.length; i++) {
      let elm = roleImagesArray[i].nativeElement;
      if (i == 0) {
        this.svgPath = "M" + (elm.offsetLeft - elm.offsetWidth / 2) + "," + (elm.offsetTop - elm.offsetHeight / 2)
      } else {
        this.svgPath += " L" + (elm.offsetLeft - elm.offsetWidth / 2) + "," + (elm.offsetTop - elm.offsetHeight / 2)
      }
    }
    this.cdRef.detectChanges();
  }

  getData() {
    const dataInDict = this.textHelperService.getInitiatives('IntroRoles')
    const datainJson = this.utils.dictToJson(dataInDict);
    this.roleData = Object.keys(datainJson).map((key) => datainJson[key]);

    this.roleActive = new Array(this.roleData.length).fill(false);
  }

  updateActiveRoles(data: Array<string>){
    this.roleActive.fill(false);

    // for(let i=0; i<this.roleActive.length; i++){
    //   this.roleActive[i] = data.includes(i+1+'') ? true : false
    // }
    for(let i=0; i < data.length; i++){
      const idx = +data[i].trim() - 1;
      this.roleActive[idx] = true;
    }

    const activeidx = +data[data.length - 1].trim() - 1;
    // this.setActiveIndex(activeidx)
  }

  setActiveIndex(index){
    if(this.activeIndex != index){
      this.previousIndex = this.activeIndex;
      this.activeIndex = index;
      this.animateCircle();
    }
  }

  animateCircle(){
    //getCurrentPercentage
    let currentPerc = this.getPercent(this.previousIndex);
    //getNewPercentage
    let newPerc = this.getPercent(this.activeIndex);
    let isLast = this.activeIndex == this.roleImagesRef.length - 1
    //move from current to new by increasing 1 every milli second
    var difference = Math.round(Math.abs(newPerc - currentPerc));
    let direction = newPerc > currentPerc ? 1 : -1; 
    this.customLoop(currentPerc, difference, direction, isLast)
  }

  customLoop(currentPerc, difference, direction, isLast = false){
    let movePerc = currentPerc + direction;
    setTimeout(() => {
      this.moveObj(movePerc)
      if(difference == 0){
        if(isLast){
            var pt = this.svgPathRef.nativeElement.getPointAtLength(movePerc);
            pt.x = Math.round(pt.x);
            pt.y = Math.round(pt.y);
            pt.x += 40;
            pt.y += 40;
            this.svgCircleRef.nativeElement.style.webkitTransform = 'translate3d(' + pt.x + 'px,' + pt.y + 'px, 0)';
        }else{
          return
        }
      }else{
        this.customLoop(movePerc, difference - 1, direction, isLast)
      }
    }, 1);
  }

  moveObj(percent){
    var pt = this.svgPathRef.nativeElement.getPointAtLength(percent);
    pt.x = Math.round(pt.x);
    pt.y = Math.round(pt.y);
    this.svgCircleRef.nativeElement.style.webkitTransform = 'translate3d(' + pt.x + 'px,' + pt.y + 'px, 0)';
  }

  getPercent(index){
    let diffObj = {
      1: 12.5,
      2: 17.5,
      3: 20,
      4: 22.5,
      5: 25,
      6:30
    }
    var diff = diffObj[index+1];
    // var diff = 10 + 2.5*(index+1);
    var percent = diff + (index/this.roleImagesRef.length *100);
    // Length of path
    var pathLength = Math.floor(this.svgPathRef.nativeElement.getTotalLength());

    // Move obj element along path based on percentage of total length
    percent = (percent * pathLength) / 100 ;
    return percent;
  }

}
