import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';

const YEAR_RANGE = 'tlInputTeamYear'
@Component({
  selector: 'summary-layout-three',
  templateUrl: './summary-layout-three.component.html',
  styleUrls: ['./summary-layout-three.component.styl']
})
export class SummaryLayoutThreeComponent implements OnInit {
  renderChart: boolean = false;

  constructor(
    private textEngineService: TextHelperService,
    private calcservice: CalcService,
    private elRef: ElementRef,
    private utils: Utils,
    private communicator: CommunicatorService
  ) { }

  commonChartOptions1:any

  ngOnInit() {
    this.getData();
  }

  ngAfterViewInit(){
    setTimeout(() => {
      this.renderChart = true;
    }, 1000);
  }

  getData(){
    this.commonChartOptions1 = {
      title: {
        text: ''
      },
      // chart: { 'height': 200, 'width': 900 },
      legend: {
        enabled: true,
        y: 0,
        reversed: false
      },
      yAxis: {
        title: {
          text: ''
        },
        labels: { format: '{value:0.0%}' },
        stackLabels: {
          enabled: false,
          format: '{y:0.}',
          style: {
            // fontWeight: 'bold',
            display: 'none'
          }
        }
      },
      plotOptions:
      {
        column:
        {
          dataLabels: {
            enabled: false,
            format: '{y:0.0%}',
            crop: false,
            overflow: true,
            inside: false
          },
        }
      },
      tooltip: {
        pointFormat: '{series.name}:{point.y: 0.0%}'
      },
    }
  }
}
