import { Component, OnInit, ElementRef, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';

@Component({
  selector: 'app-intro-market-forecast',
  templateUrl: './intro-market-forecast.component.html',
  styleUrls: ['./intro-market-forecast.component.styl']
})
export class IntroMarketForecastComponent implements OnInit {
  Scenarios = [];
  activeIndex = 0
  forecastSubscriber: Subscription;
  forecastData;

  constructor(
    private elRef: ElementRef, private  route: ActivatedRoute, private router: Router,
    private communicator: CommunicatorService
  ) { }

  ngOnInit() {
    this.getcarouselData();
  }

  ngAfterContentInit(){
    this.forecastSubscriber =this.communicator.getEmitter(AppConstants.MARKET_FORECAST).subscribe((data)=>{
      this.forecastData= data;
      this.getcarouselData();
  })
  }

  getcarouselData() {
    this.Scenarios = [
      {
        text: 'Carousel1Label1',
        data: this.forecastData==false ? '../../../assets/images/intro/market_forecast/forecast.jpg' : '../../../assets/images/intro/market_forecast/mf1.png'
      },
      {
        text: 'Carousel1Label2',
        data: '../../../assets/images/intro/market_forecast/mf2.png'
      },
      {
        text: 'Carousel1Label3',
        data: '../../../assets/images/intro/market_forecast/mf3.png'
      },
      {
        text: 'Carousel1Label4',
        data: '../../../assets/images/intro/market_forecast/mf4.png'
      },
      {
        text: 'Carousel1Label5',
        data: '../../../assets/images/intro/market_forecast/mf5.png'
      }
    ]
  }

  updateActiveIndex(action){
    let prevCarousalElement = this.elRef.nativeElement.querySelector('.carousel-item.active')
    let prevIndex = +prevCarousalElement.dataset.index;
    if(action == 'prev'){
      if(this.activeIndex == 0 || prevIndex == 0){
        return
      }
      this.activeIndex = prevIndex - 1;
    }

    if(action == 'next'){
      if(this.activeIndex == (this.Scenarios.length-1) || prevIndex == (this.Scenarios.length-1) ){
        return
      }
      this.activeIndex = prevIndex + 1;
    }

  }

  ngOnDestroy(){
    this.forecastSubscriber.unsubscribe();
  }
}
