import { Component, OnInit, Input } from '@angular/core';
import { Subscription } from 'rxjs';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { LocationService } from 'src/app/shared/location.service';

@Component({
  selector: 'simple-check',
  templateUrl: './simple-check.component.html',
  styleUrls: ['./simple-check.component.styl']
})
export class SimpleCheckComponent implements OnInit {
  @Input() ref;
  @Input() yearRef;
  @Input() periodRef;
  isChecked: Boolean
  subsription: Subscription;
  isFollower: any;

  constructor(
    private calcService: CalcService,
    private locationService: LocationService
  ) { }

  ngOnInit() {
    this.isFollower = this.locationService.isFollower;

    if (this.periodRef) {
      const period = this.calcService.getValueForYear(this.periodRef, this.yearRef);
      if (typeof period !== 'undefined' && period !== '') {
        this.ref += '_P' + period;
      }
    }

    this.checkStatus()
    if(this.isFollower){
      this.subsription = this.calcService.getObservable().subscribe(() => {
        this.checkStatus();
      })
    }
  }

  ngOnDestroy() {
    if (this.subsription) {
      this.subsription.unsubscribe();
    }
  }

  checkStatus() {
    const val = this.calcService.getValueForYear(this.ref, this.yearRef);
    this.isChecked = val == "1";
  }
}
