import { Component, OnInit, HostListener } from '@angular/core';
import { Subscription } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { LocationService } from 'src/app/shared/location.service';
import { AppConstants } from 'src/app/app-constants';

@Component({
  selector: 'app-prework',
  templateUrl: './prework.component.html',
  styleUrls: ['./prework.component.styl']
})
export class PreworkComponent implements OnInit {
  activeTab: string = "welcome";
  routeSubscription: Subscription;
  assertiveText: string = '';
  stringConstants = AppConstants.INTRO_TABS;
  navIndex = "0"
  isIntro: boolean;
  @HostListener('document:keydown.arrowup', ['$event'])
  @HostListener('document:keydown.arrowdown', ['$event'])
  @HostListener('document:keydown.arrowleft', ['$event'])
  @HostListener('document:keydown.arrowright', ['$event'])
  public handleKeyboardEvent(event) {
    switch (event.keyCode) {
      case 39:
      case 40:
        $('.nav-item:focus').next().focus();
        event.preventDefault();
        event.stopPropagation();
        break;
      case 37:
      case 38:
        $('.nav-item:focus').prev().focus();
        event.preventDefault();
        event.stopPropagation();
        break;
    }
  }

  constructor(
    private activatedRoute: ActivatedRoute,
    private locationService: LocationService,
    private router: Router,
  ) {
    this.routeSubscription = activatedRoute.params.subscribe(
      (param: any) => {
          let activeTab = (param['tabid']) ? param['tabid'] : null;
          this.activeTab = activeTab;
      });
  }

  ngOnInit() {
    this.stringConstants = AppConstants.INTRO_TABS;
    this.isIntro = this.router.url.indexOf('intro/') > -1 ? true : false;
  }

  setactiveTab(e, _subTab: string): any {
    if (_subTab === null) return;
    this.assertiveText = _subTab + (e.target.classList.contains('active') ? 'selected' : 'not selected');
    if(this.isIntro){
      const url = '/intro/prework/' + _subTab;
      this.locationService.setLocation(url, 0);
      this.router.navigate([url]);
    }else{
      const url = this.router.url.replace('introduction/prework/' + this.activeTab, 'introduction/prework/' + _subTab)
      // this.router.navigate([{ outlets: { 'secondary-nav': url } }], {relativeTo: this.activatedRoute.r});
      this.router.navigateByUrl(url)
    }
}

}
