import { ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';
import { InformationPopupComponent } from 'src/app/shared/information-popup/information-popup.component';
import { LocationService } from 'src/app/shared/location.service';

@Component({
  selector: 'app-execution',
  templateUrl: './execution.component.html',
  styleUrls: ['./execution.component.styl']
})
export class ExecutionComponent implements OnInit {
  private subscriber: Subscription;
  isDisabled = false;
  currentPeriod;
  isLoading: boolean = false;

  constructor(
    private communicator: CommunicatorService,
    private textEngineService: TextHelperService,
    private calcservice: CalcService,
    private elRef: ElementRef,
    private utils: Utils,
    private router: Router,
    private cdRef: ChangeDetectorRef,
    private locationService: LocationService
  ) { }

  ngOnInit() {
    // this.communicator.trigger(AppConstants.ACTIVE_ROLE, "3")    
    this.currentPeriod = this.calcservice.getValueForYear('tlInput_Exec_Current_Period', AppConstants.REFS.CURRENT_ROUND)
    this.checkIfSubmited();

    this.subscriber = this.calcservice.getObservable().subscribe(() => {
      this.checkIfSubmited();
      // this.currentPeriod = this.calcservice.getValueForYear('tlInput_Exec_Current_Period', AppConstants.REFS.CURRENT_ROUND)

      if (this.isDisabled) {
        this.subscriber.unsubscribe();
      }
    });
    this.communicator.trigger(AppConstants.ACTIVE_ROLE, "1,2,3,4,5,6")
  }

  ngOnDestroy() {
    this.communicator.trigger(AppConstants.ACTIVE_ROLE, null)
  }


  onSubmit() {
    this.isLoading = true;
    this.cdRef.detectChanges();

    let arrPromise = [];
    let routeToSummary = false;

    this.currentPeriod = this.calcservice.getValueForYear('tlInput_Exec_Current_Period', AppConstants.REFS.CURRENT_ROUND);// getting current period
    arrPromise.push(this.calcservice.setValueForYear('tlInput_Exec_P' + this.currentPeriod + '_Complete', 1, AppConstants.REFS.CURRENT_ROUND)) //setting the current period completed to 1

    //let period = +this.currentPeriod < AppConstants.MAX_PERIOD ? +this.currentPeriod+1 : 4; 
    // converting & incrementing the period
    if (parseInt(this.currentPeriod) < AppConstants.MAX_PERIOD) {
      this.currentPeriod = parseInt(this.currentPeriod) + 1;
      arrPromise.push(this.calcservice.setValueForYear('tlInput_Exec_Current_Period', this.currentPeriod, AppConstants.REFS.CURRENT_ROUND)) // setting the incremented period
    } else {
      routeToSummary = true;
    }


    Promise.all(arrPromise).then(() => {
      this.isLoading = false;
      this.cdRef.detectChanges();

      if (routeToSummary) {
        let url = ['dashboard', 'summary'];
        this.locationService.setLocation(url, 1);
        this.router.navigate(url);
      }else{
        this.locationService.setLocationAsCurrentURL(1)
      }
    })
    this.checkIfSubmited();
  }

  checkIfSubmited() {
    let val: any = this.calcservice.getValueForYear('tlInput_Exec_P' + AppConstants.MAX_PERIOD + '_Complete', AppConstants.REFS.CURRENT_ROUND)
    this.isDisabled = val != "0" ? true : false;
  }
}
