import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'lodash';
import { StorageService } from '../../shared/storage.service';
import { CalcService, CommunicatorService, TextService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';

@Component({
    selector: 'ism-nav-bar',
    templateUrl: './nav-bar.component.html',
    styleUrls: ['./nav-bar.component.styl']
})
export class NavBarComponent implements OnInit {
    private subscription: Subscription;
    public currentYear: number;
    public currentPeriod: number;
    public simComplete: any;
    isInitiativeSubmitted: Boolean= true;
    isPlanningSubmitted: Boolean= true;
    isExecutionSubmitted: Boolean= true;

    constructor(
        private router: Router,
        private storage: StorageService,
        private calcService: CalcService,
        private storageService: StorageService,
        private communicator: CommunicatorService,
        private textEngineService: TextService
    ) { }

    ngOnInit() {
        this.getCurrentYear();
        this.checkSimComplete();

        this.subscription = this.calcService.getObservable().subscribe(() => {
            this.getCurrentYear();
            this.checkSimComplete();
          });
    }

    ngOnDestroy(){
        if(this.subscription){
            this.subscription.unsubscribe()
        }
    }


    getCurrentYear() {
        this.currentYear = +this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
        this.currentPeriod = +this.calcService.getValueForYear(AppConstants.REFS.CURRENT_PERIOD, AppConstants.REFS.CURRENT_ROUND);
    }

    checkSimComplete() {
        // this.simComplete = Number(this.calcService.getValue('tlInputSimComplete')) == 1;

        // check if initiative is submitted
        this.isInitiativeSubmitted= +this.calcService.getValueForYear(AppConstants.REFS.SI_COMPLETED, AppConstants.REFS.CURRENT_ROUND) != 0;
        // check if planning is submitted
        this.isPlanningSubmitted= +this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) !=0 
        // check if execution is submitted
        this.isExecutionSubmitted = +this.calcService.getValueForYear('tlInput_Exec_P'+AppConstants.MAX_PERIOD+'_Complete', AppConstants.REFS.CURRENT_ROUND) != 0
    }

}
