import { Component, OnInit, OnDestroy, Output, EventEmitter, ElementRef, ViewChild, Input, AfterViewInit } from '@angular/core';
import { CalcService, CommunicatorService, Constants, NumberFormatting } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { LocationService } from '../location.service';


@Component({
  selector: 'isma-toggle-slider',
  templateUrl: './toggle-slider.component.html',
  styleUrls: ['./toggle-slider.component.styl']
})
export class ToggleSliderComponent implements OnInit {
  @Input() ref
  @Input() periodRef
  @Input() yearRef

  @Input() lableRight; 
  @Input() lableLeft;
  
  @Input() disabled = false;
  toggleValue;

  @Output() toggleUpdated = new EventEmitter();
  subsription: Subscription;
  isFollower;

  constructor(
    public calcService: CalcService,
    private communicator: CommunicatorService, 
    private locationService: LocationService
    ) {}

  ngOnInit() {
    this.isFollower = this.locationService.isFollower;
    
    if (this.periodRef) {
      const period = this.calcService.getValueForYear(this.periodRef, this.yearRef);
      if (typeof period !== 'undefined' && period !== '') {
        this.ref += '_P' + period;
      }
    }

    this.subsription = this.calcService.getObservable().subscribe(() => {
      this.checkStatus();
    })

    this.checkStatus();
    this.toggleUpdated.emit(this.toggleValue)
  }

  ngOnDestroy(){
    if(this.subsription){
      this.subsription.unsubscribe();
    }
  }

  checkStatus(){
    const val = this.calcService.getValueForYear(this.ref, this.yearRef);
    this.toggleValue = val != '0'    
  }

  toggle(){
    if(this.disabled){
      return
    }
    const val = this.toggleValue ? 0 : 1;
    this.calcService.setValueForYear(this.ref, val, this.yearRef);
    this.toggleUpdated.emit(!this.toggleValue)
  }

  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.keyCode === 13 || event.keyCode === 32) {
      // enter and space
      event.preventDefault()
      event.stopPropagation();
      event.stopImmediatePropagation();

      this.toggle();
    }else if(event.keyCode === 37 && this.toggleValue){
      //left arrow
      event.preventDefault()
      event.stopPropagation();
      event.stopImmediatePropagation();

      this.toggle();

    }else if(event.keyCode === 39 && !this.toggleValue){
      //right arrow
      event.preventDefault()
      event.stopPropagation();
      event.stopImmediatePropagation();

      this.toggle();

    }
  }
}
