import { Component, OnInit, OnDestroy, Input, HostListener } from '@angular/core';
import { HttpWrapperService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { AppConfig } from 'src/app/app.config';
import { Utils } from '../utils/utils';


@Component({
  selector: 'ism-accordian',
  templateUrl: './accordian.component.html',
  styleUrls: ['./accordian.component.styl']
})

export class AccordianComponent implements OnInit, OnDestroy {
  
  @Input() headerKey;
  @Input() tooltipKey;
  @Input() activeRoleIndex;
  isCollapsed: Boolean = true;
  @HostListener('focus', ['$event']) onFocus(e) {
    if(this.activeRoleIndex){
      this.updateRoles()
    }
  }

  constructor(
    private utils: Utils
  ) {
  }

  ngOnInit() {
    
  }

  toggleAccordian() {
    this.isCollapsed = !this.isCollapsed;
    if(!this.isCollapsed){
      this.utils.forceResize()
    }
  }

  ngOnDestroy() {
  }

  updateRoles(){
    this.utils.setRoleFocus = this.activeRoleIndex;
  }

  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.keyCode === 13 || event.keyCode === 32) {
        // space and enter
        this.toggleAccordian();            
    }
}
}
