import { Component, Input, OnDestroy, OnInit, ViewChild, EventEmitter, Output } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, SyncService } from '@btsdigital/ngx-isomer-core';
import { LocationService } from 'src/app/shared/location.service';
import * as Collections from 'typescript-collections';
import { TextHelperService as TextService } from '../../../shared/text-service.extended';
import { Utils } from '../../../shared/utils/utils';
import { AppConfig } from 'src/app/app.config';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';

@Component({
    selector: 'single-select',
    templateUrl: './single-select.html',
    styleUrls: ['./single-select.styl']
})

export class SingleSelect implements OnInit, OnDestroy {
    public choices = [];
    private isViewed = false;
    public answer: number = -1;
    @Input() id: string;
    @Input() scene: Collections.Dictionary<string, string>;
    public readyToSubmit = false;
    @Input() disabled: boolean = false;
    @Input() showFeedback:Boolean;
    @Output() onDecisionSubmit = new EventEmitter();
    @Output() onFeedbackClose = new EventEmitter();
    isFollower;

    constructor(private textengineService: TextService, private calcService: CalcService,
        private syncService: SyncService,
        private locationService: LocationService,
        private router: Router, private utils: Utils, private appConfig: AppConfig) { };

    ngOnInit() {
        this.isFollower = this.locationService.isFollower;
        
        var curVal = this.calcService.getValue("tlInput" + this.scene.getValue("ID"));

        if (curVal != "0") {
            // Answer is stored as A,B,C in model where as used as 1,2,3,... in html/ts
            this.answer = curVal.charCodeAt(0) - 64;
            if (!this.disabled) {
                this.readyToSubmit = true;
            }
        }

        this.choices = this.textengineService.getSingleSelectChoices(this.id);        
    }

    onSelect($event) {
        if (!this.disabled) {
            this.readyToSubmit = true;
        }
    }

    submit() {
        
    }

    ngOnDestroy() {
    }

    isReadyToSubmit(): boolean {
        return (this.readyToSubmit !== undefined) ? this.readyToSubmit : false;
    }
    onSubmit() {
        if (this.isFollower) {
            return
        }

        // Setting value as A, B, C, ...
        this.calcService.setValue("tlInput" + this.scene.getValue("ID"), String.fromCharCode(64 + (+this.answer)) );
        // this.calcService.setValue("tlInput_READ_" + this.scene.getValue("ID"), 1);
        this.onDecisionSubmit.emit();
        // now navigate to feedback
        // this.locationService.setLocation(this.utils.generateFeedbackUrl(this.id), 1);
        // this.router.navigate(this.utils.generateFeedbackUrl(this.id));
    }

    onClose(){
        this.onFeedbackClose.emit();
    }

    cancelEvent(event) {
        if (event) {
          event.preventDefault();
          event.stopPropagation();
          event.stopImmediatePropagation();
        }
      }
}
