import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'planning-material',
  templateUrl: './planning-material.component.html',
  styleUrls: ['./planning-material.component.styl']
})
export class PlanningMaterialComponent implements OnInit {
  @Input() isDisable: Boolean = false;

  commonChartOptions = {
    title: {
      text: ''
    },
    chart: { 
      // 'height': 300, 
      // 'width': 885, 
      'type': 'column' 
    },
    legend: {
      enabled: true,
      x: 0,
      y: 5,
      reversed: false,
      verticalAlign: 'top',
      floating: true,
      align: 'right'
    },
    yAxis: {
      title: {
        text: ''
      },
      labels: { format: '{value:0}' },
      stackLabels: {
        enabled: false,
        format: '{1:0}',
        style: {
          // fontWeight: 'bold',
          display: 'none'
        }
      }
    },
    plotOptions:
    {
      column:
      {
        dataLabels: {
          enabled: false,
          format: '{y:0}',
          crop: false,
          overflow: true,
          inside: false
        },
      }
    },
    tooltip: {
      pointFormat: '{series.name}:{point.y: 0}'
    },
  }
  renderChart: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit(){
    setTimeout(() => {
      this.renderChart = true;
    }, 1000);
  }

}
