import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { LocationService } from 'src/app/shared/location.service';


@Component({
    selector: 'isma-calc-checkbox',
    templateUrl: './calc-checkbox.component.html',
    styleUrls: ['./calc-checkbox.component.styl'],
    providers: []
})

export class CalcCheckboxComponent implements OnInit, OnDestroy {
    @Input() ref;
    @Input() yearRef;
    @Input() periodRef;
    @Input() disabled = false;
    isChecked: Boolean
    subsription: Subscription;
    isFollower;

    constructor(
        private calcService: CalcService,
        private locationService: LocationService
    ) { }

    ngOnInit() {
        this.isFollower = this.locationService.isFollower;
        
        if (this.periodRef) {
            const period = this.calcService.getValueForYear(this.periodRef, this.yearRef);
            if (typeof period !== 'undefined' && period !== '') {
                this.ref += '_P' + period;
            }
        }

        this.checkStatus()
        this.subsription = this.calcService.getObservable().subscribe(() => {
            this.checkStatus();
        })
    }

    ngOnDestroy() {
        if (this.subsription) {
            this.subsription.unsubscribe();
        }
    }

    checkStatus() {
        const val = this.calcService.getValueForYear(this.ref, this.yearRef);
        this.isChecked = val == "1"
    }

    onClick() {
        if(this.disabled){
            return;
        }
        const val = this.isChecked ? 0 : 1;
        this.calcService.setValueForYear(this.ref, val, this.yearRef)
    }

    handleKeyboardEvent(event: KeyboardEvent) {
        if (event.keyCode === 13 || event.keyCode === 32) {
            event.preventDefault()
            event.stopPropagation();
            event.stopImmediatePropagation();

            this.onClick();
        }
    }
}
