import { Component, OnInit, Input,EventEmitter, Output, ViewChild, TemplateRef, ViewChildren } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { CommonModalComponent } from '../common-modal/common-modal.component';
import { TextHelperService } from '../text-service.extended';


@Component({
    selector: 'ism-confirmation-popup',
    templateUrl: './confirmation-popup.html',
    styleUrls: ['./confirmation-popup.styl']
})
export class ConfirmationPopup implements OnInit {
    @Input() headerTextKey: string = "Confirm";
    @Input() messageHeader: string

    @Input() proceedText: string;
    @Input() retakeText: string
    
    @Output() confirmClicked: EventEmitter<any> = new EventEmitter();
    @Output() confirmRetake: EventEmitter<any> = new EventEmitter();
    @ViewChild('confirmModal', {static: false}) public confirmModalRef: CommonModalComponent;

    retakeBtn:Boolean;
    dashboardSubmitBtn:Boolean=false;

    constructor(private textHelperService: TextHelperService) { }

    ngOnInit() {
     }

    showAlert(data){
        this.confirmModalRef.openModal();
        this.retakeBtn = data=='retake' ? true : false
    }

    onCloseClicked() {
        this.confirmModalRef.closeModal();
    }

    onConfirmClicked() {
        this.confirmClicked.emit();
        this.onCloseClicked();
    }

    onRetakeClicked(){
        this.onCloseClicked();
        this.confirmRetake.emit();
    }

}
