import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';
import { HostListener } from '@angular/core';
import { Subscription } from 'rxjs';
import { LocationService } from 'src/app/shared/location.service';
import { debounceTime } from 'rxjs/operators';
import { AppConstants } from 'src/app/app-constants';
// import * as $ from 'jquery';
@Component({
  selector: 'app-quiz-single-select',
  templateUrl: './quiz-single-select.component.html',
  styleUrls: ['./quiz-single-select.component.styl']
})
export class QuizSingleSelectComponent implements OnInit, OnDestroy {
  @Input() data;
  @Output() buttonClick = new EventEmitter();
  choices:any;

  selectedAnswer = 0;
  correctAnswer = 0;
  id: string;
  scene: any;
  isAnswered: boolean = false;
  public readyToSubmit = false;
  isFollower;
  calcSubscription: Subscription;

  constructor(
    private textService: TextService,
    private textHelperService: TextHelperService,
    private router: Router,
    private utils: Utils,
    private calcService: CalcService,
    private locationService: LocationService
  ) { }

  

  @HostListener('document:keyup.arrowup', ['$event'])
  @HostListener('document:keyup.arrowdown', ['$event'])
  @HostListener('document:keyup.arrowleft', ['$event'])
  @HostListener('document:keyup.arrowright', ['$event'])
  public handleKeyboardEvent(event) {
    switch (event.keyCode) {
      case 39:
      case 40:
        $('.choice:focus').next().focus();
        event.preventDefault();
        event.stopPropagation();
        event.stopImmediatePropagation();
        break;
        case 37:
        case 38:
          $('.choice:focus').prev().focus();
          event.preventDefault();
          event.stopPropagation();
          event.stopImmediatePropagation();
        break;
    }
  }

  ngOnInit() {
    // this.choices = this.textHelperService.getQuizChoices(this.data.ID)
    this.isFollower = this.locationService.isFollower;

    if (this.data) {
      this.id = this.data.ID;
      this.scene = this.data.scene;

      this.choices = this.textHelperService.getQuizChoices(this.data.ID)
      this.correctAnswer = this.data.correctAnswer

      this.isAnswered = +this.calcService.getValue(this.data.range) != 0;
      if(this.isAnswered){
        this.selectedAnswer = +this.calcService.getValue(this.data.range);
      }

      this.calcSubscription = this.calcService.getObservable().pipe(debounceTime(AppConstants.CALC_DEBOUNCE_TIME)).subscribe(()=>{
        this.isAnswered = +this.calcService.getValue(this.data.range) != 0;
        if(this.isAnswered){
          this.selectedAnswer = +this.calcService.getValue(this.data.range);
        }
      })
    }

  }

  ngOnDestroy(){
    if(this.calcSubscription){
      this.calcSubscription.unsubscribe();
    }
  }

  onSelect(index) {
    if(this.isAnswered){
      return;
    }
    this.readyToSubmit = true;
    this.selectedAnswer = index;
  }

  onSubmit(){
    this.isAnswered = true;
    this.calcService.setValue(this.data.range, +this.selectedAnswer);
    this.buttonClick.emit();
  }


}
