import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavigationEnd, Router, UrlTree } from '@angular/router';
import { CalcService, TextService, StorageService, SyncService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { LocationService } from '../../shared/location.service';
import { environment } from 'src/environments/environment';
import { AppConfig } from 'src/app/app.config';
import { AppConstants } from 'src/app/app-constants';

const INITIAL_VALUE = "Mayer"

@Component({
    selector: 'registration',
    templateUrl: './registration.html',
    styleUrls: ['./registration.styl']
})
export class Registration implements OnInit {
    isFollower = false;
    teamNameValue='';
    errorMsg:Boolean = false;
    

    constructor(
        private calcService: CalcService, 
        private textengineService: TextService, 
        private router: Router, 
        private locationService: LocationService, 
        private storageService: StorageService, 
        private syncService: SyncService, 
        private appConfig: AppConfig,
        private communicator: CommunicatorService
    ) { };

    ngOnInit() {
        this.isFollower = this.locationService.isFollower;
        
        // this.teamNameValue = this.calcService.getValue('tlInputTeamName_R1') ? this.calcService.getValue('tlInputTeamName_R1') : '';
        // this.storageService.getValue(AppConstants.INTRO_COMPLETE).then((val)=>{
        //     if(val==undefined || val != 'true')
        //         this.teamNameValue= '';
        // });

        this.teamNameValue = this.calcService.getValue('tlInputTeamName_R1');
        
        //Using range name to check as we have Leader Follower
        let simSarted = this.calcService.getValue(AppConstants.REFS.SIM_STARTED) == "1"
        if(!simSarted && this.teamNameValue == INITIAL_VALUE){
            this.teamNameValue = '';
        }
    }

    navigate() {
        if(this.teamNameValue.length!=0){
            // this.storageService.setValue(AppConstants.INTRO_COMPLETE, 'true')
            this.calcService.setValue(AppConstants.REFS.SIM_STARTED, 1)
            this.calcService.setValue('tlInputTeamName_R1',this.teamNameValue)

            let url = ['dashboard', 'initiatives'];
            this.locationService.setLocation(url, 1);
            this.router.navigate(url);
            this.errorMsg = false;
        }
        else
            this.errorMsg = true;
    }

}
