import { Component, ElementRef, OnInit } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';
import { Router } from '@angular/router';
import { LocationService } from 'src/app/shared/location.service';

const YEAR_RANGE = 'tlInputTeamYear'
@Component({
  selector: 'app-summary',
  templateUrl: './summary.component.html',
  styleUrls: ['./summary.component.styl']
})
export class SummaryComponent implements OnInit {
  private subscriber: Subscription;
  currentYear = 0;

  constructor(
    private textEngineService: TextHelperService,
    private calcservice: CalcService,
    private elRef: ElementRef,
    private utils: Utils,
    private communicator: CommunicatorService,
    private router: Router,
    private locationService: LocationService
  ) { }

  ngOnInit() {
    this.currentYear = +this.calcservice.getValue(YEAR_RANGE);
  }

  onSubmit() {

    let currentRound = Number(this.calcservice.getValue('tlInputRound'));
    this.calcservice.setValue('tlInputRound', currentRound + 1);

    let url = ['dashboard', 'initiatives'];
    this.locationService.setLocation(url,1)
    this.router.navigate(url);

  }

}
