import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CalcModelLoadedGuard } from '@btsdigital/ngx-isomer-core';
import { IntroComponent, Registration, PreworkComponent, IntroQuizComponent } from './intro/intro';
import { ChoiceComponent } from './views/WiB-Events/choice/choice';
import { ChoiceMadeGuard } from './views/WiB-Events/choice/choice-made-guard';
import { DashboardRoute } from './views/dashboard/dashboardroute/dashboard.route';
import { InfoComponent } from './views/WiB-Events/info/info';
import { LogoutComponent } from './shared/logout/logout.component';
import { SplashComponent } from './shared/splash/splash.component';
import { CongratulationsComponent } from './views/congratulations/congratulations.component';
import { ExecutionComponent } from './views/execution/execution.component';
import { PlanningComponent } from './views/planning/planning.component';
import { InitiativesComponent } from './views/initiatives/initiatives.component';
import { ProductFormulaComponent } from './views/product-formula/product-formula.component';
import { CustomerOrderComponent } from './views/customer-order/customer-order.component';
import { OverviewComponent } from './views/overview/overview.component';
import { IntroductionComponent } from './views/introduction/introduction.component';
import { SummaryComponent } from './views/summary/summary.component';
import { GraphOrdersComponent } from './views/graph-orders/graph-orders.component';

const routes: Routes = [
  {
    path: 'splash',
    component: SplashComponent
  },
  {
    path: 'intro',
    component: IntroComponent,
    canActivate: [CalcModelLoadedGuard],
    children: [{
      path: 'registration',
      component: Registration
    }, {
      path: 'prework/:tabid',
      component: PreworkComponent,
      data:{
        list: ['welcome', 'product-solution', 'market-forecast', 'kpi', 'process-checklist', 'roles']
      }
    }, {
      path: 'quiz',
      component: IntroQuizComponent
    }, {
      path: 'quiz/:idx',
      component: IntroQuizComponent,
      data:{
        list: [0,5] //initial question ID
      }
    }, 
    {
      path: '',
      redirectTo: '/intro/prework/welcome',
      pathMatch: 'full'
    }
  ],
  },
  {
    path: 'dashboard',
    canActivate: [CalcModelLoadedGuard],
    component: DashboardRoute,
    children: [
      {
        path: 'message/:id',
        component: InfoComponent,
        outlet: 'secondary-nav',
        data:{
          list: ['MarketUpdate1_R1', 'Mat1_R1', 'Prod1_R1', 'Distr1_R1']
        }
      },
      {
        path: 'meeting/:id',
        component: ChoiceComponent,
        canActivate: [ChoiceMadeGuard],
        outlet: 'secondary-nav',
        data:{
          list: ['Event1_R1']
        }
      },
      {
        path: 'feedback/:id',
        component: ChoiceComponent,
        outlet: 'secondary-nav',
        data:{
          list: ['Event1_R1']
        }
      },
      {
        path: 'initiatives',
        component: InitiativesComponent
      },
      {
        path: 'planning',
        component: PlanningComponent
      },
      {
        path: 'execution',
        component: ExecutionComponent
      },
      {
        path: 'product-formula',
        component: ProductFormulaComponent,
        outlet: 'secondary-nav'
      },
      {
        path: 'customer-order',
        component: CustomerOrderComponent,
        outlet: 'secondary-nav'
      },
      {
        path: 'graph-order',
        component: GraphOrdersComponent,
        outlet: 'secondary-nav'
      },
      {
        path: 'overview',
        component: OverviewComponent,
        outlet: 'secondary-nav'
      },
      {
        path: 'introduction',
        component: IntroductionComponent,
        outlet: 'secondary-nav',
        children:[
          {
            path: 'prework/:tabid',
            component: PreworkComponent,
            data:{
              list: ['welcome', 'product-solution', 'market-forecast', 'kpi', 'process-checklist', 'roles']
            }
          }
        ]
      },
      {
        path: 'congratulations',
        component: CongratulationsComponent
      },
      {
        path: 'summary',
        component: SummaryComponent
      }
    ]
  },  
  {
    path: 'intro',
    component: InfoComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path: 'logout',
    component: LogoutComponent
  },
  {
    path: '',
    redirectTo: '/splash',
    pathMatch: 'full'    
  }
]

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})

export class AppRoutingModule { }
