import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';
import { LocationService } from 'src/app/shared/location.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-intro-quiz',
  templateUrl: './intro-quiz.component.html',
  styleUrls: ['./intro-quiz.component.styl']
})
export class IntroQuizComponent implements OnInit {

  checkIfProceedDisabledFlag :Boolean;
  public showArrowIcon:Boolean = true;
  proceedText = "Start Round ";
  retakeText = "Retake Quiz";
  firstActiveQuestionIndex = 0;
  MaxQuestions = 5;
  animationName = ''
  routeSubscription: Subscription;

  constructor(
    private textService: TextService,
    private textHelperService: TextHelperService,
    private router: Router,
    private utils: Utils,
    private calcService: CalcService,
    private locationService: LocationService,
    private activatedRoute: ActivatedRoute,
  ) { 
    this.routeSubscription = activatedRoute.params.subscribe(
      (param: any) => {
          let activeIdx = (param['idx']) ? +param['idx'] : 0;
          if(this.firstActiveQuestionIndex < activeIdx){
            this.animationName = 'slideInRight'
          }else if(this.firstActiveQuestionIndex > activeIdx){
            this.animationName = 'slideInLeft'
          }
          
          this.firstActiveQuestionIndex = activeIdx;
          setTimeout(() => {
            this.animationName = ''
          }, 1000);
      });
  }

  quizData:any;

  ngOnInit() {
    this.getData();
    this.checkIfProceedEnabled();
    this.proceedText = this.proceedText+this.calcService.getValue('tlInputRound');
    
  }

  getData(){
    let data = this.textHelperService.getInitiatives('IntroQuiz')
    let data1 = this.utils.dictToJson(data);
    this.quizData = Object.keys(data1).map((key) => {
      data1[key]['SrNo'] = Number(key.split('_').pop());
      return data1[key]});
    this.quizData.sort((a,b)=> (a.SrNo > b.SrNo) ? 1 : ((b.SrNo > a.SrNo) ? -1 : 0));

  }

  checkIfProceedEnabled(){
    this.checkIfProceedDisabledFlag = false;
    for(let i= 0; i<this.quizData.length; i++){
      let obj = this.quizData[i]
      let isSubmitted = this.calcService.getValue(obj.range) != '0';
      if(!isSubmitted){
        this.checkIfProceedDisabledFlag = true;
        break;
      }
    }

  }

  onSubmit(){
    let url = ['intro', 'registration'];
    this.locationService.setLocation(url, 1)
    this.router.navigate(url);
  }

  onRetake(){
    console.log('retake');
    for(let i= 0; i<this.quizData.length; i++){
      let obj = this.quizData[i]
      this.calcService.setValue(obj.range, 0);
    }

    /**on retake resetting the quiz data**/
    this.quizData=[];
    this.getData();
    this.checkIfProceedEnabled()
  }

  onPrev(){
    // this.animationName = 'slideInLeft'
    // this.firstActiveQuestionIndex = this.firstActiveQuestionIndex - this.MaxQuestions > 0 ? this.firstActiveQuestionIndex - this.MaxQuestions : 0;
    let idx = this.firstActiveQuestionIndex - this.MaxQuestions > 0 ? this.firstActiveQuestionIndex - this.MaxQuestions : 0;
    let url = ['intro', 'quiz', idx];
    this.locationService.setLocation(url, 1)
    this.router.navigate(url);
  }
  
  onNext(){
    // this.animationName = 'slideInRight'
    // this.firstActiveQuestionIndex = this.firstActiveQuestionIndex + this.MaxQuestions < this.quizData.length ? this.firstActiveQuestionIndex + this.MaxQuestions : this.quizData.length - this.MaxQuestions;
    
    let idx = this.firstActiveQuestionIndex + this.MaxQuestions < this.quizData.length ? this.firstActiveQuestionIndex + this.MaxQuestions : this.firstActiveQuestionIndex;
    let url = ['intro', 'quiz', idx];
    this.locationService.setLocation(url, 1)
    this.router.navigate(url);
  }
}
