import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'app-overview',
  templateUrl: './overview.component.html',
  styleUrls: ['./overview.component.styl']
})
export class OverviewComponent implements OnInit {
  public isClosing = false;
  public isOpening = true;
  public modalShow: boolean = true;
  overviewHeader:any;
  pageTitle;

  constructor(
    private router: Router,
    public activatedRoute: ActivatedRoute,
    private textEngineService: TextService,
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.isClosing = false;
    this.isOpening = true;
    this.getData();
  }

  onClose() {
    this.isClosing = true;
    this.isOpening = false;
    this.modalShow = false;

    setTimeout(() => this.router.navigate(['.', { outlets: { 'secondary-nav': null } }], { relativeTo: this.activatedRoute.parent }), 1000);
  }

  getData(){
    let title= this.textEngineService.getText('OverviewTitle')
    let period= this.calcService.getValue('tlInput_Exec_Current_Period_R1');
    this.pageTitle = title+" "+period;
    
      this.overviewHeader=[
        {
          key:'overviewHeader1'
        },
        {
          key:'overviewHeader2'
        },
        {
          key:'overviewHeader3'
        },
        {
          key:'overviewHeader4',
          subkey:'OverviewLabel17'
        },
        {
          key:'overviewHeader5',
          subkey:'OverviewLabel18'
        },
        {
          key:'overviewHeader6'
        },
        {
          key:'overviewHeader7',
          subkey:'OverviewLabel19'
        }
      ]
  }
}
