import { Component, OnInit, ElementRef } from '@angular/core';

@Component({
  selector: 'app-intro-process-checklist',
  templateUrl: './intro-process-checklist.component.html',
  styleUrls: ['./intro-process-checklist.component.styl']
})
export class IntroProcessChecklistComponent implements OnInit {
  Scenarios = [];
  activeIndex = 0

  constructor(
    private elRef: ElementRef
  ) { }


  ngOnInit() {
    this.getcarouselData();
  }

  getcarouselData() {
    this.Scenarios = [
      {
        text: 'Carousel2Label1',
        data: '../../../assets/images/intro/process_checklist/pc1.png'
      },
      {
        text: 'Carousel2Label2',
        data: '../../../assets/images/intro/process_checklist/pc2.png'
      },
      {
        text: 'Carousel2Label3',
        data: '../../../assets/images/intro/process_checklist/pc3.png'
      },
      {
        text: 'Carousel2Label4',
        data: '../../../assets/images/intro/process_checklist/pc4.png'
      },
      {
        text: 'Carousel2Label5',
        data: '../../../assets/images/intro/process_checklist/pc5.png'
      },
      {
        text: 'Carousel2Label6',
        data: '../../../assets/images/intro/process_checklist/pc6.png'
      }
    ]
  }

  updateActiveIndex(action) {
    let prevCarousalElement = this.elRef.nativeElement.querySelector('.carousel-item.active')
    let prevIndex = +prevCarousalElement.dataset.index;
    if (action == 'prev') {
      if (this.activeIndex == 0 || prevIndex == 0) {
        return
      }
      this.activeIndex = prevIndex - 1;
    }

    if (action == 'next') {
      if (this.activeIndex == (this.Scenarios.length - 1) || prevIndex == (this.Scenarios.length - 1)) {
        return
      }
      this.activeIndex = prevIndex + 1;
    }

  }

}
