import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-product-formula',
  templateUrl: './product-formula.component.html',
  styleUrls: ['./product-formula.component.styl']
})
export class ProductFormulaComponent implements OnInit {
  public isClosing = false;
  public isOpening = true;
  public modalShow: boolean = true;

  constructor(
    private router: Router,
    public activatedRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    this.isClosing = false;
    this.isOpening = true;
  }

  onClose() {
    this.isClosing = true;
    this.isOpening = false;
    this.modalShow = false;
        
    setTimeout(() => this.router.navigate(['.', {outlets: {'secondary-nav': null}}], { relativeTo: this.activatedRoute.parent }), 1000);
  }
}
