import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';

@Component({
  selector: 'app-customer-order-button',
  templateUrl: './customer-order-button.component.html',
  styleUrls: ['./customer-order-button.component.styl']
})
export class CustomerOrderButtonComponent implements OnInit, OnDestroy {
  rangeNamesToCheck = []
  valuesArray = []
  hasNewNotification = false;
  subscription: Subscription;
  currentPeriod;
  currentRound;
  planningSubmitted = false;


  constructor(
    private calcService: CalcService,
    private router: Router
  ) { }

  ngOnInit() {
    this.currentPeriod = this.calcService.getValueForYear(AppConstants.REFS.CURRENT_PERIOD,AppConstants.REFS.CURRENT_ROUND)
    this.currentRound = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND)
    this.initializeRangeName();
    this.checkIfNotificationUpdated();
    this.planningSubmitted = +this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) == 1
    
    this.subscription = this.calcService.getObservable().subscribe(()=>{
      this.planningSubmitted = +this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) == 1
      let currentPeriod = this.calcService.getValueForYear(AppConstants.REFS.CURRENT_PERIOD,AppConstants.REFS.CURRENT_ROUND)
      let currentRound = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND)
      if(currentPeriod != this.currentPeriod || currentRound != this.currentRound){
        this.currentPeriod = currentPeriod;
        this.currentRound = currentRound;
        this.initializeRangeName()
        this.checkIfNotificationUpdated()
      }
    })
  }

  checkIfNotificationUpdated(){
    for(let i = 0; i<this.rangeNamesToCheck.length; i++){
      const val = this.calcService.getValue(this.rangeNamesToCheck[i])
      if(this.valuesArray[i] != val){
        this.valuesArray[i] = val;
        this.hasNewNotification = true
      }
    }
  }

  initializeRangeName(){
    this.rangeNamesToCheck = [
      "tlOutput_Prod1_Reg1_P"+this.currentPeriod+"_Order_Check_R"+this.currentRound,
      "tlOutput_Prod2_Reg1_P"+this.currentPeriod+"_Order_Check_R"+this.currentRound,
      "tlOutput_Prod1_Reg2_P"+this.currentPeriod+"_Order_Check_R"+this.currentRound,
      "tlOutput_Prod2_Reg2_P"+this.currentPeriod+"_Order_Check_R"+this.currentRound
    ]

  }

  ngOnDestroy(){
    if(this.subscription){
      this.subscription.unsubscribe()
    }
  }

  onClick(){
    this.hasNewNotification = false;
    this.router.navigate(['dashboard/', {outlets: {'secondary-nav':'customer-order'}}])
  }

}
