import { Injectable, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { LoggerService } from '@btsdigital/pulseutilities';
import { AppConstants } from '../app-constants';

@Injectable()
export class LocationService implements OnInit, OnDestroy {
    private location = '';
    private subscription: any;
    isFollower = false;

    constructor(private calcService: CalcService, private router: Router, private communicator: CommunicatorService, private logger: LoggerService) {
        // let calcPromise = this.calcService.getApi();

        // calcPromise.then((api) => {
            this.subscription = this.calcService.getObservable().subscribe(() => {
                let status = '';
                let newLocation = '';
                let leader = this.calcService.getValue(AppConstants.REFS.LEADER_TRACK_FOREMAN);
                let locationJSON = this.calcService.getValue(AppConstants.REFS.LEADER_LOCATION);
                if (locationJSON.length > 4) {
                    // this skips the 'JSON' text at the beginning of the string
                    // this text is there so that jsCalc doesn't try and treat the string as a number
                    locationJSON = locationJSON.slice(4);
                }
                let locationObj = JSON.parse(locationJSON);
                newLocation = locationObj['location'];
                status = locationObj['status'];

                if ((!leader || (leader == '0') || (leader == null) || (leader == undefined) || (leader == '')) && (newLocation != this.location)) {
                    this.location = newLocation;
                    let url: any = []; let urlIsString = false;
                    let commaLoc = newLocation.indexOf(',');
                    if (commaLoc != -1) {
                        url = newLocation.split(',');
                        let partsArray = newLocation.split(':');
                        if (partsArray.length == 3) {
                            // let outlets = { partsArray[0] { partsArray[1] : partsArray[2] }};
                        }
                    }else{
                        urlIsString = true
                        url = this.location;
                    }
                    if (status == '1') {
                        if(urlIsString){
                            this.router.navigateByUrl(url)
                        }else{
                            this.router.navigate(url);
                        }
                    }
                }
            })
        // });
    }

    ngOnInit() {
    }

    getLocation() {
        return this.location;
    }

    setLocation(loc, status) {
        if (loc.constructor === Array) {
            this.location = loc.toString();
        } else {
            this.location = loc;
        }

        if(this.location.indexOf('[object Object]') != -1){
            setTimeout(() => {
                this.setLocationAsCurrentURL(status)
            }, 100);
            return;
        }

        var locationObj = new Object();
        locationObj['location'] = this.location;
        locationObj['status'] = status;
        var locationStr = JSON.stringify(locationObj);
        console.log('sending location: ' + locationStr);

        // adding 'JSON' so that jsCalc doesn't try and treat the string as a number
        this.calcService.setValue(AppConstants.REFS.LEADER_LOCATION, ('JSON' + locationStr));
    }

    setLocationAsCurrentURL(status){
        let url = this.router.url;
        this.setLocation(url, status)
    }

    getLeaderStatus() {
        let leader = this.calcService.getValue(AppConstants.REFS.LEADER_TRACK_FOREMAN);
        let follower = this.calcService.getValue(AppConstants.REFS.LEADER_TRACK);

        // if the follower range does not exist (which it should, even for the leader), default to being a leader
        // so that the user has access to decisions
        if ((follower == undefined) || (follower == "") || (follower == "0")) {
            this.logger.log("getLeaderStatus: follower undefined or blank (" + follower + ") (no one to follow): defaulting to leader");
            return 1;
        } else {
            // if there's any non-zero, non-blank value in the foreman, then leader, else follower
            if ((leader != undefined) && (leader != "0") && (leader != "")) {
                this.logger.log("getLeaderStatus: leader: " + leader);
                return 1;
            } else {
                this.logger.log("getLeaderStatus: follower: " + follower + ", with leader: " + leader);
                return 0;
            }
        }
    }

    getFollowerStatus() {
        let leader = this.calcService.getValue(AppConstants.REFS.LEADER_TRACK_FOREMAN);
        let follower = this.calcService.getValue(AppConstants.REFS.LEADER_TRACK);

        if ((follower == undefined) || (follower == "") || (follower == "0")) {
            this.logger.log("getFollowerStatus: follower undefined or blank (" + follower + ") >> not follower");
            return 0;
        } else {
            if ((leader == undefined) || (leader == "") || (leader == "0")) {
                this.isFollower = true;
                this.logger.log("getFollowerStatus: follower valid - leader undefined or blank >> follower");
                return 1;
            } else {
                this.logger.log("getFollowerStatus: follower valid - leader valid >> not follower");
                return 0;
            }
        }
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }
}