import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';
import { InformationPopupComponent } from 'src/app/shared/information-popup/information-popup.component';
import { debounceTime } from 'rxjs/operators';

@Component({
  selector: 'app-progress-bar',
  templateUrl: './progress-bar.component.html',
  styleUrls: ['./progress-bar.component.styl']
})
export class ProgressBarComponent implements OnInit, OnDestroy {
  currentPeriod = 1;
  totalPeriod=4;
  progressValue=0.5;
  progressFormat='0%';
  progressScaler=1;
  calcSubscription: Subscription;
  planningCompleted;
  @ViewChild('infoPopup', {static: false}) infoPopupRef: InformationPopupComponent;

  constructor(
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.currentPeriod = +this.calcService.getValueForYear('tlInput_Exec_Current_Period', AppConstants.REFS.CURRENT_ROUND)
    this.planningCompleted = this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) == '1'
    this.getProgress()
    this.calcSubscription = this.calcService.getObservable().pipe(debounceTime(AppConstants.CALC_DEBOUNCE_TIME)).subscribe(()=>{
      this.getCurrentPeriod();
      this.getProgress();
    })
  }

  ngOnDestroy(){
    if(this.calcSubscription){
      this.calcSubscription.unsubscribe();
    }
  }

  getCurrentPeriod(){
    let currentPeriod = +this.calcService.getValueForYear('tlInput_Exec_Current_Period', AppConstants.REFS.CURRENT_ROUND)
    if(currentPeriod != this.currentPeriod){
      currentPeriod > this.currentPeriod ? this.infoPopupRef.showAlert(): null;
      this.currentPeriod = currentPeriod;
    }

    //Open Welcome to Period 1 popup on planning complete
    let planningCompleted = this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) == '1'
    if(!this.planningCompleted && planningCompleted){
      this.infoPopupRef.showAlert()
    }
    this.planningCompleted = planningCompleted;
  }

  getProgress(){
    this.progressValue = +this.calcService.getValueForYear('tlOutput_Progress', AppConstants.REFS.CURRENT_ROUND, true)
  }

}
