import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from 'src/app/app-constants';

@Component({
  selector: 'app-customer-order',
  templateUrl: './customer-order.component.html',
  styleUrls: ['./customer-order.component.styl']
})
export class CustomerOrderComponent implements OnInit {
  public isClosing = false;
  public isOpening = true;
  public modalShow: boolean = true;
  currentRound;
  currentPeriod;
  
  activePeriod = 1;
  activeRound = 1;
  thresholdPeriod;
  
  constructor(
    private router: Router,
    public activatedRoute: ActivatedRoute,
    private calcservice: CalcService
  ) { }



  ngOnInit() {
    this.isClosing = false;
    this.isOpening = true;
    this.getData();
  }

  onClose() {
    this.isClosing = true;
    this.isOpening = false;
    this.modalShow = false;

    setTimeout(() => this.router.navigate(['.', { outlets: { 'secondary-nav': null } }], { relativeTo: this.activatedRoute.parent }), 1000);
  }

  getData() {
    this.currentPeriod = this.calcservice.getValueForYear('tlInput_Exec_Current_Period', AppConstants.REFS.CURRENT_ROUND);
    this.currentRound = this.calcservice.getValue("tlInputRound")

    this.activeRound = this.currentRound;
    this.activePeriod = this.currentPeriod;
    this.thresholdPeriod = this.currentPeriod;
  }

  onRoundChange(){
    if(this.activeRound < this.currentRound){
      // unlock all
      this.thresholdPeriod = 4
    }else if(this.activeRound == this.currentRound){
      // check period
      this.thresholdPeriod = this.currentPeriod;
    }

    this.activePeriod = 1;
  }
}
