import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { Utils } from '../../../shared/utils/utils';
import { CommunicatorService, StorageService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from 'src/app/app-constants';

@Component({
    templateUrl: './routes.html',
    styleUrls: ['./routes.styl'],
})
export class DashboardRoute implements OnInit, OnDestroy {
    private subscription: Subscription;
    public hideNavigation: boolean = false;
    showSecondaryNav = false;
    secondaryNavActive = false;
    showPrimaryOutlet: boolean;
    constructor(private router: Router, private utils: Utils,
        private communicator: CommunicatorService,
        private storage: StorageService
    ) { }

    ngOnInit() {
        this.subscription = this.router.events.subscribe(event => {
            if (event instanceof NavigationEnd) {
                this.updateStatus();
            }
        });

        this.updateStatus();
    }

    updateStatus() {
        this.showSecondaryNav = this.getSecondaryNavStatus();
        this.secondaryNavActive = this.utils.isOutletRouteActive(AppConstants.SECONDARY_OUTLET, this.router.url);
    }

    getSecondaryNavStatus() {
        let outlets: Array<string> = [AppConstants.ROUTES.DASHBOARD],
        isActive = false;

        outlets.forEach(outlet => {
            let result = this.utils.isRouteActive(outlet, this.router.url);
            if (result) {
                isActive = true;
            }
        });
        return isActive;
    }

    ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }

    onActivate(event : any) {
        this.showPrimaryOutlet = true;
    }

    onDeactivate(event : any) {
        this.showPrimaryOutlet = false;
    }

    onSecondaryRouteActivate(event : any) {       
        let currentFocus = document.activeElement;
        this.utils.setFocusElement = currentFocus;
    }

    onSecondaryRouteDeactivate(event : any) { 
        let prevFocus = this.utils.getFocusElement;
        if(prevFocus){
            prevFocus.focus();
        }        
    }
}
