import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'app-graph-orders',
  templateUrl: './graph-orders.component.html',
  styleUrls: ['./graph-orders.component.styl']
})
export class GraphOrdersComponent implements OnInit {
  public isClosing = false;
  public isOpening = true;
  public modalShow: boolean = true;

  chartData:any;
  activePeriod = 1;
  activeRound = 1;

  constructor(
    private router: Router,
    private calcService: CalcService,
    public activatedRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    this.isClosing = false;
    this.isOpening = true;
    this.getGraphData();
  }

  onClose() {
    this.isClosing = true;
    this.isOpening = false;
    this.modalShow = false;

    setTimeout(() => this.router.navigate(['.', { outlets: { 'secondary-nav': null } }], { relativeTo: this.activatedRoute.parent }), 1000);
  }

  getGraphData(){
    this.chartData = {
      title: {
        text: ''
      },
      legend: {
        enabled: true,
        y: 0,
        reversed: false
      },
      yAxis: {
        title: {
          text: ''
        },
        labels: { format: '{value:0}' },

      },
      plotOptions:
      {
        column:
        {
          dataLabels: {
            enabled: true,
            format: '{y:0}',
            crop: false,
            overflow: true,
            inside: false
          },
        }
      },
      tooltip: {
        pointFormat: '{series.name}:{point.y: 0}'
      },
    }
  }

}
