import { Component, OnInit, HostListener, ViewChild, OnDestroy } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { CommonModalComponent } from '../common-modal/common-modal.component';
import { Router, Route } from '@angular/router';
import { PasswordComponent } from '../password-popup/password.component';
import { AppConstants } from 'src/app/app-constants';
import { Subscription } from 'rxjs';
import { environment } from 'src/environments/environment';

const KEY_NUM_3 = 51;

@Component({
  selector: 'isma-calc-debugger',
  templateUrl: './calc-debugger.component.html',
  styleUrls: ['./calc-debugger.component.styl']
})
export class CalcDebuggerComponent implements OnInit, OnDestroy {
  isCalcDebuggerActive: boolean = false;
  listener: Subscription;
  @ViewChild('calcDebuggerModal', { static: true }) calcDebuggerModalRef: CommonModalComponent;
  @ViewChild('passwordModal', { static: true }) passwordModalRef: PasswordComponent;
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_3) {
      if(environment.production){
        this.passwordModalRef.showModal();
      }else{
        this.showCalcDebugger()
      }
    }
  }
  routes = []
  hasAtleastOneParam = false;

  constructor(
    private calcService: CalcService,
    private communicator: CommunicatorService,
    private router: Router
  ) { }

  ngOnInit() {
    this.initializeRoutes()
    this.listener = this.communicator.getEmitter(AppConstants.EVENTS.MANUALLY_OPEN_DEBUGGER).subscribe(()=>{
      if(environment.production){
        this.passwordModalRef.showModal();
      }else{
        this.showCalcDebugger()
      }
    })
  }

  ngOnDestroy(){
    if(this.listener){
      this.listener.unsubscribe()
    }
  }
  
  initializeRoutes(){
    this.getpath('', this.router.config);
    this.hasAtleastOneParam = this.routes.reduce((previousValue, currentValue)=>{
      return previousValue || currentValue.hasParameters
    },this.hasAtleastOneParam)
    console.log("hasAtleastOneParam", this.hasAtleastOneParam)
    console.log("routes", this.routes)
  }

  getpath(parent: String, config: Route[]) {
    for (let i = 0; i < config.length; i++) {
      const route = config[i];
      let finalPath = parent + '/' + route.path;
      if(route.outlet){
        finalPath = parent + '/(' + route.outlet + ':' + route.path + ')'
      }

      let obj = {
        name: route.path.split('/')[0],
        path: finalPath,
        hasParameters: route.path.split('/').length > 1,
        parameterName: route.path.split('/').length > 1 ? route.path.split('/')[1] : null,
        parameterList: route.data && route.data.list ? route.data.list : []
      }
      this.routes.push(obj)

      if (route.children) {
        let currentPath;
        if(route.outlet){
          currentPath = route.path ? parent + '/(' + route.outlet + ':' + route.path : parent
        }else{
          currentPath = route.path ? parent + '/' + route.path : parent;
        }
        this.getpath(currentPath, route.children);
      }
    }
  }

  goToRoute(routeObj, parameterValue){
    let path = routeObj.path
    if(routeObj.hasParameters){
      path = path.replace(routeObj.parameterName, parameterValue)
    }

    this.router.navigateByUrl(path)
  }

  showCalcDebugger() {
    this.isCalcDebuggerActive = true;
    this.calcDebuggerModalRef.openModal();
  }

  hideCalcDebugger() {
    this.calcDebuggerModalRef.closeModal();
    this.isCalcDebuggerActive = false;
  }

  triggerExport(value) {
    this.communicator.trigger(AppConstants.EVENTS.IMPORT_EXPORT_TRIGGER, value)
  }

  triggerCommunication(key) {
    this.communicator.trigger(AppConstants.EVENTS[key])
    this.hideCalcDebugger();
  }
}
