import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';

@Component({
  selector: 'stock-materials',
  templateUrl: './stock-materials.component.html',
  styleUrls: ['./stock-materials.component.styl']
})
export class StockMaterialsComponent implements OnInit {
  public isClosing = false;
  public isOpening = true;
  public modalShow: boolean = true;
  overviewHeader:any;
  pageTitle;

  constructor(
    private router: Router,
    public activatedRoute: ActivatedRoute,
    private textEngineService: TextService,
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.getData();
  }


  getData(){
    let title= this.textEngineService.getText('overviewTitle')
    let period= this.calcService.getValue('tlInput_Exec_Current_Period_R1');
    this.pageTitle = title+" "+period;
    
      this.overviewHeader=[
        {
          key:'overviewHeader1'
        },
        {
          key:'overviewHeader2'
        },
        {
          key:'overviewHeader3'
        },
        {
          key:'overviewHeader4'
        },
        {
          key:'overviewHeader5'
        },
        {
          key:'overviewHeader6'
        },
        {
          key:'overviewHeader7'
        }
      ]
  }
}
