import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-check-order',
  templateUrl: './check-order.component.html',
  styleUrls: ['./check-order.component.styl']
})
export class CheckOrderComponent implements OnInit, OnDestroy {

  tickValue;
  @Input() ref = '';
  @Input() yearRef = '';
  subscription: Subscription;

  constructor(
    private router: Router,
    public activatedRoute: ActivatedRoute,
    private calcservice: CalcService
  ) { }

  ngOnInit() {
    this.getTickIcon();

    this.subscription = this.calcservice.getObservable().subscribe(()=>{
      this.getTickIcon()
    })
  }

  ngOnDestroy(){
    if(this.subscription){
      this.subscription.unsubscribe();
    }
  }

  ngOnChanges(){
    this.getTickIcon();
  }


  getTickIcon(){
    this.tickValue= this.calcservice.getValueForYear(this.ref, this.yearRef);
  }
}
