import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Router } from '@angular/router';
import { AppConstants } from 'src/app/app-constants';

@Component({
  selector: 'app-graph-order-button',
  templateUrl: './graph-order-button.component.html',
  styleUrls: ['./graph-order-button.component.styl']
})
export class GraphOrderButtonComponent implements OnInit {
  subscription: Subscription;
  currentPeriod;
  currentRound;
  planningSubmitted = false;


  constructor(
    private calcService: CalcService,
    private router: Router
  ) { }

  ngOnInit() {
    this.currentPeriod = this.calcService.getValueForYear(AppConstants.REFS.CURRENT_PERIOD,AppConstants.REFS.CURRENT_ROUND)
    this.currentRound = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND)

    this.planningSubmitted = +this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) == 1
    
    this.subscription = this.calcService.getObservable().subscribe(()=>{
      this.planningSubmitted = +this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) == 1
      this.currentPeriod = this.calcService.getValueForYear(AppConstants.REFS.CURRENT_PERIOD,AppConstants.REFS.CURRENT_ROUND)
      this.currentRound = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND)
    })
  }

  ngOnDestroy(){
    if(this.subscription){
      this.subscription.unsubscribe()
    }
  }

  onClick(){
    this.router.navigate(['dashboard/', {outlets: {'secondary-nav':'graph-order'}}])
  }
}
