import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from 'src/app/app-constants';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';

@Component({
  selector: 'execution-shipment',
  templateUrl: './execution-shipment.component.html',
  styleUrls: ['./execution-shipment.component.styl']
})
export class ExecutionShipmentComponent implements OnInit {
  @Input() isDisable:Boolean= false;
  currentPeriod;
  currentRound;

  ChartOptions = {
    chart:{
      height: '250px'
    },
    title: {
      text: ''
    },
    // chart: { 'height': 200, 'width': 900 },
    yAxis: {
      visible: false,
      max: 4000
    },
    plotOptions: {
      column: {
        dataLabels: {
          enabled: true,
          format: '{y:0,0}',
          crop: false,
          overflow: true,
          inside: false
        },
      }
    },
    // tooltip: {
    //   pointFormat: '{series.name}:{point.y: 0,0}'
    // }
  }

  constructor(
    private communicator: CommunicatorService,
    private textEngineService: TextHelperService,
    private calcservice: CalcService,
    private elRef: ElementRef,
    private utils: Utils
  ) { }


  ngOnInit() {
    this.currentRound = this.calcservice.getValue(AppConstants.REFS.CURRENT_ROUND)
    this.currentPeriod = this.calcservice.getValueForYear(AppConstants.REFS.CURRENT_PERIOD , AppConstants.REFS.CURRENT_ROUND)
  }

}
