import { Component, OnInit, Input } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from 'src/app/app-constants';

@Component({
  selector: 'app-historical-planning-data',
  templateUrl: './historical-planning-data.component.html',
  styleUrls: ['./historical-planning-data.component.styl']
})
export class HistoricalPlanningDataComponent implements OnInit {
  activeRound = 1;
  currentRound;

  constructor(
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.currentRound = +this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND)
  }

}
