import { Component, ElementRef, HostListener, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { CalcService, TextService, SyncService, ConnectService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { environment } from 'src/environments/environment';
import { StorageService } from '../../shared/storage.service';
import { AppConfig } from 'src/app/app.config';
import { LocationService } from 'src/app/shared/location.service';
import { Subscription } from 'rxjs';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';
import { AppConstants } from 'src/app/app-constants';

declare var $: any;
const KEYCODE_NUM0 = 48;

@Component({
  selector: 'my-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.styl']
})
export class DashboardComponent implements OnInit, OnDestroy {
  currentYear: number;
  simComplete: any;
  showMenu: boolean = false;
  isFollower = 0;
  showLogo: boolean = true;
  isIntro: boolean = false;
  subscription: any;
  routingSubscription: Subscription;
  @ViewChild('logoutModal', {static: false}) public logoutModalRef: CommonModalComponent;
  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEYCODE_NUM0) {
      this.resetSim();
    }
  }

  @HostListener('document:keydown.arrowup', ['$event'])
  @HostListener('document:keydown.arrowdown', ['$event'])
  @HostListener('document:keydown.arrowleft', ['$event'])
  @HostListener('document:keydown.arrowright', ['$event'])
  public handleKeyboardEvent(event) {
    switch (event.keyCode) {
      case 39:
      case 40:
        if($('.menu-options-button:focus').length){
          $('.menu-options-button:focus').next().focus();
          event.preventDefault();
          event.stopPropagation();
        }
        break;
      case 37:
      case 38:
        if($('.menu-options-button:focus').length){
          $('.menu-options-button:focus').prev().focus();
          event.preventDefault();
          event.stopPropagation();
        }
        break;
    }
  }

  constructor(private calcService: CalcService, private textEngineService: TextService, 
              private storageService: StorageService, private syncService: SyncService, private connectservice: ConnectService,
              private router: Router, private communicator: CommunicatorService,  private appConfig: AppConfig,
              private locationService: LocationService
              ) { }

  ngOnInit() {
    this.isIntro = this.router.url.indexOf('intro/') > -1 ? true : false;
    this.showLogo = this.router.url.indexOf('registration') == -1;
    this.routingSubscription = this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.showLogo = this.router.url.indexOf('registration') == -1;
        this.isIntro = this.router.url.indexOf('intro/') > -1 ? true : false;
      }
    });
    
    this.getCurrentYear();
    this.checkSimComplete();
    
    if (this.appConfig.configuration.connectToPulse) {
      this.checkIfFollower();
      // setTimeout(() => {
      //   this.checkIfFollower();
      // }, 2000);
    }
    this.subscription = this.calcService.getObservable().subscribe(() => {
      this.getCurrentYear();
      this.checkSimComplete();

      if (this.appConfig.configuration.connectToPulse) {
        this.checkIfFollower();
      }
    });
  }

  getCurrentYear() {
    this.currentYear = Number(this.calcService.getValue('tlInputTeamYear'));
  }

  checkSimComplete() {
    this.simComplete = Number(this.calcService.getValue('tlInputSimComplete'));
    // if (this.simComplete) {
    //   this.router.navigate(['dashboard/congratulations'])
    // }
  }

  resetSim() {
    let confirmText = this.textEngineService.getText("ResetSim");
    let confirmResponse = confirm(confirmText);
    if (confirmResponse) {
      this.storageService.clearAll().then(() => {
        window.location.reload();
      });
    }
    // localStorage.clear();
    // window.location.reload();
  }

  showAlert() {
    this.logoutModalRef.openModal();
  }

  hideAlert(confirmed) {

    if(confirmed)
    this.router.navigate(['/logout']);

    this.logoutModalRef.closeModal();
  }

  checkIfFollower() {
    let val = this.locationService.getFollowerStatus();// returns 1 for follower and 0 for leader
    if (this.isFollower != val) {
        this.isFollower = val
        console.log("Leader/Follower Status: " + this.isFollower);
        if (this.isFollower == 1) {
            if (!document.body.classList.contains("follower")) {
                document.body.classList.add("follower");
            }
        } else {
            if (document.body.classList.contains("follower")) {
                document.body.classList.remove("follower");
            }
        }
    }
  }
  
  openMenu($event) {
    this.showMenu = !this.showMenu;
    $event.preventDefault();
    $event.stopPropagation();
  }

  closeMenu() {
    this.showMenu = false;
  }

  getHelp() {
    this.closeMenu();
    this.communicator.trigger(AppConstants.EVENTS.RAISE_HELP)
  }

  ngOnDestroy() {
    this.routingSubscription.unsubscribe();
    this.subscription.unsubscribe();
  }

  openAdminPanel(){
    this.communicator.trigger(AppConstants.EVENTS.MANUALLY_OPEN_DEBUGGER)
  }
}
