import { Component, OnInit, ElementRef, ViewChildren, QueryList, Renderer2, ChangeDetectorRef, Input, SimpleChanges, SimpleChange } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { LocationService } from 'src/app/shared/location.service';
import { Subscription } from 'rxjs';
import * as Collections from 'typescript-collections';
import { StorageService } from 'src/app/shared/storage.service';
import { TextHelperService as TextService } from '../../../shared/text-service.extended';
import { LinkStatus } from '../link/linkstatus';
import { LinkComponent } from '../link/link.component';
import { AppConstants } from 'src/app/app-constants';
import { auditTime } from 'rxjs/operators';
import { indexOf } from 'typescript-collections/dist/lib/arrays';

@Component({
  selector: 'app-event-drawer',
  templateUrl: './event-drawer.component.html',
  styleUrls: ['./event-drawer.component.styl']
})
export class EventDrawerComponent implements OnInit {
  public visibleCardsArray = [];
  public allCardsArray = [];
  currentRound: string = '';
  private meetingStatusSubscription: Subscription;
  private messageStatusSubscription: Subscription;
  public newMeetingsCount = 0;
  public newMessagesCount = 0;
  public totalMeetings = 0;
  public totalMessages = 0;
  public isClosing = true;
  public isOpening = false;
  private scenes: Collections.Dictionary<string, any>;
  showInbox: boolean = false;
  private subscriber: Subscription;
  @ViewChildren('eventLink') eventLinkRef: QueryList<LinkComponent>;
  array;
  cardsVisited=0;

  constructor(
    private renderer: Renderer2,
    private textengineService: TextService,
    private calcService: CalcService,
    private storageService: StorageService,
    private locationService: LocationService,
    private commuicator: CommunicatorService,
    private cdRef: ChangeDetectorRef
  ) { }

  ngOnInit() {


    // this.meetingStatusSubscription = this.commuicator.getEmitter(AppConstants.MEETING_STATUS).subscribe(status => {
    //   if (status) {
    //     this.newMeetingsCount = status.getNewLinksCount();
    //     this.totalMeetings = status.getTotalLinksCount();
    //     this.updateVisibleCardsArray()
    //   }
    // });
    // this.messageStatusSubscription = this.commuicator.getEmitter(AppConstants.MESSAGE_STATUS).subscribe(status => {
    //   if (status) {
    //     this.newMessagesCount = status.getNewLinksCount();
    //     this.totalMessages = status.getTotalLinksCount();
    //     this.updateVisibleCardsArray()
    //   }
    // });
    this.initializeCardsData();
    this.subscriber = this.calcService.getObservable().pipe(
      auditTime(AppConstants.CALC_DEBOUNCE_TIME)
    ).subscribe(()=>{

      let round= this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
      if(round == this.currentRound){
        this.updateVisibleCardsArray()
        this.checkCardsRead()
      }
      else{
        this.initializeCardsData();
      }
    })

    // this.visibleCardsArray= this.array;
  }

  initializeCardsData(){
    this.currentRound = this.calcService.getValueForYear(AppConstants.REFS.CURRENT_ROUND);
    this.fetchSceneData();
    this.updateVisibleCardsArray()
    this.initializeStatus();
    this.checkCardsRead()
  }

  ngOnDestroy() {
    // this.meetingStatusSubscription.unsubscribe();
    // this.messageStatusSubscription.unsubscribe();
    this.subscriber.unsubscribe();
  }

  initializeStatus() {
    this.storageService.setValueInDictionary(AppConstants.MEETING_STATUS, new LinkStatus());
    this.storageService.setValueInDictionary(AppConstants.MESSAGE_STATUS, new LinkStatus());
  }

  fetchSceneData() {
    this.allCardsArray = [];
    this.scenes = this.textengineService.getScenes();
    let self = this;
    this.scenes.forEach((key => {
      var localScene = [];
      const _scene: Collections.Dictionary<string, any> = this.scenes.getValue(key);

      _scene.keys()
        .forEach((key) => {
          localScene[key] = _scene.getValue(key);
        });

      let ID = _scene.getValue('ID');
      localScene["decisionRange"] = "tlInput" + ID;
      // localScene["roundRange"] = "tlInputRound" + ID;
      localScene["triggerRange"] = "Triggers_" + ID;
      localScene["readRange"] = "tlInput_READ_" + ID;
      let sceneRound = ID[ID.length - 1];

      if (localScene["PageType"] == "Read Update") {
        if (self.currentRound == sceneRound) {
          localScene["type"] = "message";
          this.allCardsArray.push(localScene);
        }
      } else if ((localScene["PageType"] == "SingleSelect") || (localScene["PageType"] == "MultiSelect")) {
        if (self.currentRound == sceneRound) {
          localScene["type"] = "meeting";
          this.allCardsArray.push(localScene);
        }
      }
    }));
    this.allCardsArray = this.allCardsArray;
    console.log(this.allCardsArray);
  }

  onPrev($event){
    this.visibleCardsArray.unshift(this.visibleCardsArray.pop())
    this.cdRef.detectChanges()
    
    // $event.preventDefault();
    //$event.stopPropagation();
  }
  
  onNext($event){
    this.visibleCardsArray.push(this.visibleCardsArray.shift())
    this.cdRef.detectChanges()

    // $event.preventDefault();
    //$event.stopPropagation();
  }

  updateVisibleCardsArray(){
    let length = this.visibleCardsArray.length;
    let oldArray = [...this.visibleCardsArray];
    
    this.visibleCardsArray = [];
    for(let i=0; i<this.allCardsArray.length;i++){
      const card = this.allCardsArray[i]
      let calcTriggerStatus: any = this.calcService.getValue(card.triggerRange);
      let isHidden = (calcTriggerStatus != 1);
      if(!isHidden){
        this.visibleCardsArray.push(card)
      }
    }

    this.visibleCardsArray.forEach(y=>{
        y["index"]= this.visibleCardsArray.indexOf(y);
      })

    if(this.visibleCardsArray.length > length){
      this.showInbox = true;
      this.isOpening = true
      this.isClosing = false
      return;
    }

    if(this.visibleCardsArray.length > 0){
      if(oldArray[this.visibleCardsArray.length - 1].ID != this.visibleCardsArray[this.visibleCardsArray.length - 1].ID){
        this.visibleCardsArray = [...oldArray]
      }
    }
    this.cdRef.detectChanges()
  }

  checkCardsRead(){
    this.cardsVisited=0;
    
    this.allCardsArray.forEach((x)=>{
      if(this.calcService.getValue(x.readRange)=='1'){
        this.cardsVisited++;
      }
    })
  }

  toggleInbox(){
    if(this.visibleCardsArray.length==0){
      return
    }

    if(this.showInbox){
      //Already open... need to close
      this.isClosing = true;
      this.isOpening = false;
      setTimeout(() => {
        this.showInbox = false;
      }, 1000);
    }else{
      //Already closed... need to open
      this.showInbox = true;
      this.isOpening = true
      this.isClosing = false
    }

  }
}
