import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { CommonModalComponent } from '../common-modal/common-modal.component';
import { Subscription } from 'rxjs';
import { SignalRService } from '@btsdigital/pulsesignalr';
import { ChannelEventName } from '@btsdigital/pulseutilities';
import { AppConstants } from 'src/app/app-constants';
import { Utils } from '../utils/utils';

@Component({
  selector: 'ism-already-logged-in',
  templateUrl: './already-logged-in.component.html',
  styleUrls: ['./already-logged-in.component.styl']
})
export class AlreadyLoggedInComponent implements OnInit, OnDestroy {
  @ViewChild('alertDialog', { static: true }) alertDialogModalRef: CommonModalComponent;
  userDetail: any;
  isChecked = false;
  listener: Subscription;

  constructor(
    private communicator: CommunicatorService,
    private signalR: SignalRService,
    private utils: Utils
  ) { }

  ngOnInit() {
    this.userDetail = this.utils.getUserObj;
    setTimeout(() => {
      // Adding timeout as signalR takes a while to get initialized
      this.subscribeToSignalR();
    }, 3000);
  }

  ngOnDestroy() {
    if (this.listener) {
      this.listener.unsubscribe();
    }
  }

  showAlert() {
    this.alertDialogModalRef.openModal();
  }

  hideAlert() {
    if (this.isChecked) {
      localStorage.setItem(AppConstants.DONT_SHOW_ALREADY_LOGGED_IN, 'true');
    }
    this.alertDialogModalRef.closeModal();
  }

  subscribeToSignalR() {
    if (this.signalR.subscribeToEvent(ChannelEventName.LogoutPreviousUser)) {
      this.listener = this.signalR.subscribeToEvent(ChannelEventName.LogoutPreviousUser).subscribe((item) => {
        if (item && item.EventName === ChannelEventName.LogoutPreviousUser) {
          if (item.Data && item.Data.participantEmail === this.userDetail.pemail) {
            let val = JSON.parse(localStorage.getItem(AppConstants.DONT_SHOW_ALREADY_LOGGED_IN))
            if (!val) {
              this.showAlert()
            }
          }
        }
      });
    }
  }

}
