import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { CommonModalComponent } from '../common-modal/common-modal.component';

@Component({
  selector: 'app-information-popup',
  templateUrl: './information-popup.component.html',
  styleUrls: ['./information-popup.component.styl']
})
export class InformationPopupComponent implements OnInit {
  @Input() infoTextKey: string = "";
  @Input() iconClassName: string = "";
  @Input() buttonText: string = "OK";
  @Input() useLightTheme = false
  @Input() className="modal-sm"
  
  @ViewChild('infoModal', {static: false}) public infoModalRef: CommonModalComponent;


  constructor() { }

  ngOnInit() {
  }

  showAlert(){
    this.infoModalRef.openModal();
  }

  onClick() {
    this.infoModalRef.closeModal();
  }
}
