import { Component, ElementRef, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';
import { debounceTime } from 'rxjs/operators';
import { Router } from '@angular/router';
import { ConfirmationPopup } from 'src/app/shared/confirmation-popup/confirmation-popup';
import { LocationService } from 'src/app/shared/location.service';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';

@Component({
  selector: 'app-planning',
  templateUrl: './planning.component.html',
  styleUrls: ['./planning.component.styl']
})
export class PlanningComponent implements OnInit, OnDestroy {
  private subscriber: Subscription;
  currentRound = 0;
  isDisabled = false;
  minPeriod = 1;
  maxPeriod = 4;
  listPlanningToExecution: Array<COPY_RANGE> = [
    //Transportation
    {
      copyfromRange: 'tlInput_Planning_Reg1Transport_Prod1',
      copyToRange: 'tlInput_Exec_Reg1Dist_Prod1'
    },
    {
      copyfromRange: 'tlInput_Planning_Reg1Transport_Prod2',
      copyToRange: 'tlInput_Exec_Reg1Dist_Prod2'
    },
    {
      copyfromRange: 'tlInput_Planning_Reg2Transport_Prod1',
      copyToRange: 'tlInput_Exec_Reg2Dist_Prod1'
    },

    {
      copyfromRange: 'tlInput_Planning_Reg2Transport_Prod2',
      copyToRange: 'tlInput_Exec_Reg2Dist_Prod2'
    },
    //Materials
    {
      copyfromRange: 'tlInput_Planning_Mat1',
      copyToRange: 'tlInput_Exec_Mat1'
    },
    {
      copyfromRange: 'tlInput_Planning_Mat2',
      copyToRange: 'tlInput_Exec_Mat2'
    },
    {
      copyfromRange: 'tlInput_Planning_Mat3',
      copyToRange: 'tlInput_Exec_Mat3'
    },
    {
      copyfromRange: 'tlInput_Planning_Mat4',
      copyToRange: 'tlInput_Exec_Mat4'
    },
    {
      copyfromRange: 'tlInput_Planning_Mat5',
      copyToRange: 'tlInput_Exec_Mat5'
    },
    {
      copyfromRange: 'tlInput_Planning_Mat6',
      copyToRange: 'tlInput_Exec_Mat6'
    },
    {
      copyfromRange: 'tlInput_Planning_Mat7',
      copyToRange: 'tlInput_Exec_Mat7'
    },
    {
      copyfromRange: 'tlInput_Planning_Mat8',
      copyToRange: 'tlInput_Exec_Mat8'
    },
    //Production Part 1
    {
      copyfromRange: 'tlInput_Planning_Line1',
      copyToRange: 'tlInput_Exec_Line1_Part1'
    },
    {
      copyfromRange: 'tlInput_Planning_Line2',
      copyToRange: 'tlInput_Exec_Line2_Part1'
    },
    {
      copyfromRange: 'tlInput_Planning_Line3',
      copyToRange: 'tlInput_Exec_Line3_Part1'
    },
    {
      copyfromRange: 'tlInput_Planning_Line4',
      copyToRange: 'tlInput_Exec_Line4_Part1'
    },
    {
      copyfromRange: 'tlInput_Planning_Line5',
      copyToRange: 'tlInput_Exec_Line5'
    },
    {
      copyfromRange: 'tlInput_Planning_Line6',
      copyToRange: 'tlInput_Exec_Line6'
    },
    //Production Part 2
    {
      copyfromRange: 'tlInput_Planning_Line1',
      copyToRange: 'tlInput_Exec_Line1_Part2'
    },
    {
      copyfromRange: 'tlInput_Planning_Line2',
      copyToRange: 'tlInput_Exec_Line2_Part2'
    },
    {
      copyfromRange: 'tlInput_Planning_Line3',
      copyToRange: 'tlInput_Exec_Line3_Part2'
    },
    {
      copyfromRange: 'tlInput_Planning_Line4',
      copyToRange: 'tlInput_Exec_Line4_Part2'
    },
  ]
  @ViewChild('confirmPopup', {static: false}) confirmPopupRef: ConfirmationPopup;
  @ViewChild('historicalData', {static: false}) historicalDataPopup: CommonModalComponent;
  @ViewChild('foreCastGraphData', {static: false}) foreCastGraphData: CommonModalComponent;
  isLoading = false;
  historicalDataVisible = false;
  showForecastGraphTab= false;

  constructor(
    private textEngineService: TextHelperService,
    private calcservice: CalcService,
    private elRef: ElementRef,
    private utils: Utils,
    private communicator: CommunicatorService,
    private router: Router,
    private locationService: LocationService
  ) { }
  


  ngOnInit() {
    this.currentRound = +this.calcservice.getValue(AppConstants.REFS.CURRENT_ROUND);
    this.checkIfSubmited();

    this.subscriber = this.calcservice.getObservable().pipe(debounceTime(AppConstants.CALC_DEBOUNCE_TIME)).pipe(debounceTime(AppConstants.CALC_DEBOUNCE_TIME)).subscribe(() => {
      this.checkIfSubmited();

      if (this.isDisabled) {
        this.subscriber.unsubscribe();
      }
    });
    this.communicator.trigger(AppConstants.ACTIVE_ROLE, "1,2,3,4,5,6")
  }
  checkIfSubmited() {
    let val: any = this.calcservice.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND);
    this.isDisabled = val != "0" ? true : false;
  }


  ngOnDestroy(){
    this.communicator.trigger(AppConstants.ACTIVE_ROLE, null)
    if(this.subscriber){
      this.subscriber.unsubscribe()
    }
  }
  onSubmit() {
    this.confirmPopupRef.onCloseClicked();
    this.isLoading = true;
    this.calcservice.setValueForYear(AppConstants.REFS.PLANNING_COMPLETED, 1, AppConstants.REFS.CURRENT_ROUND);
    console.log("Before Initialise Execution Data- ", window.performance.now())
    this.initializeExecutionData().then(()=>{
      console.log("After Initialise Execution Data- ", window.performance.now())
      this.isLoading = false;
      let url = ['dashboard', 'execution'];
      this.locationService.setLocation(url, 1);
      this.router.navigate(url);
    })
  }
  
  initializeExecutionData(): Promise<any>{
    console.log("Inside Initialise Execution Data Before For Loop- ", window.performance.now())
    let arrPromise = [];
    
    for(let i=0, len = this.listPlanningToExecution.length; i<len; i++){
      let fromRange = this.listPlanningToExecution[i].copyfromRange;
      let toRange = this.listPlanningToExecution[i].copyToRange;
      for(let period = this.minPeriod; period<=this.maxPeriod; period++){
        let val = this.calcservice.getValueForYear(fromRange+'_P'+period, AppConstants.REFS.CURRENT_ROUND, true)
        arrPromise.push(this.calcservice.setValueForYear(toRange+'_P'+period, val, AppConstants.REFS.CURRENT_ROUND)) 
      }
    }
    
    console.log("Inside Initialise Execution Data After For Loop- ", window.performance.now())
    return Promise.all(arrPromise);
  }


  showHistoricalData(){
    this.historicalDataVisible = true;
    this.historicalDataPopup.openModal();
  }

  hideHistoricalData(){
    this.historicalDataPopup.closeModal();
    this.historicalDataVisible = false;
  }

  showForecastGraphData(){
    this.foreCastGraphData.openModal();
    this.communicator.trigger(AppConstants.MARKET_FORECAST, false);
  }

  hideForecastGraphData(){
    this.foreCastGraphData.closeModal()
  }

  showTab(){

    let initiative1R1 = this.calcservice.getValue("tlInputSI1_R1");
    let initiative2R1 = this.calcservice.getValue("tlInputSI2_R1");
    
    let initiative1R2 = this.calcservice.getValue("tlInputSI1_R2");
    let initiative2R2 = this.calcservice.getValue("tlInputSI2_R2")
    
    let initiative1R3 = this.calcservice.getValue("tlInputSI1_R3");
    let initiative2R3 = this.calcservice.getValue("tlInputSI2_R3")
    
    let condition1= initiative1R1=="5" || initiative2R1=="5";
    let condition2= initiative1R2=="5" || initiative2R2=="5";
    let condition3= initiative1R3=="5" || initiative2R3=="5";

    
      if( condition1 ){  
        this.showForecastGraphTab= true; //showing the tab in R1 and in further rounds, if statistical forecasting is selected in R1
      }

      else if( condition1 || condition2 ){
        this.showForecastGraphTab= true;  //showing the tab in R2 and in further rounds, if statistical forecasting is selected in R2
      }

      else if((condition1 || condition2) || condition3){
        this.showForecastGraphTab= true;   //showing the tab in R3 and in further rounds, if statistical forecasting is selected in R3
      }

      return this.showForecastGraphTab;
  }

  showMarketForecast(){
    this.router.navigate(['/dashboard', {outlets: {'secondary-nav':'introduction/prework/market-forecast'}}])
  }

}

interface COPY_RANGE {
  copyfromRange: string,
  copyToRange: string
}