import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from 'src/app/app-constants';

@Component({
  selector: 'supplier-transit',
  templateUrl: './supplier-transit.component.html',
  styleUrls: ['./supplier-transit.component.styl']
})
export class SupplierTransitComponent implements OnInit {
  public isClosing = false;
  public isOpening = true;
  public modalShow: boolean = true;
  overviewHeader:any;
  pageTitle;
  currentPeriod;
  var;

  constructor(
    private router: Router,
    public activatedRoute: ActivatedRoute,
    private textEngineService: TextService,
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.getData();
  }


  getData(){
    let title= this.textEngineService.getText('overviewTitle')
    this.currentPeriod = +this.calcService.getValueForYear('tlInput_Exec_Current_Period', AppConstants.REFS.CURRENT_ROUND)
    this.pageTitle = title+" "+this.currentPeriod; 
    
      this.overviewHeader=[
        {
          key:'overviewHeader1'
        },
        {
          key:'overviewHeader2'
        },
        {
          key:'overviewHeader3'
        },
        {
          key:'overviewHeader4'
        },
        {
          key:'overviewHeader5'
        },
        {
          key:'overviewHeader6'
        },
        {
          key:'overviewHeader7'
        }
      ]
  }
}
