import { Component, OnInit, ViewChildren, ElementRef, QueryList, AfterViewInit, ViewChild, ChangeDetectorRef, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { TextService } from '@btsdigital/ngx-isomer-core';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import { Utils } from 'src/app/shared/utils/utils';
import { LocationService } from 'src/app/shared/location.service';

@Component({
  selector: 'app-intro-roles',
  templateUrl: './intro-roles.component.html',
  styleUrls: ['./intro-roles.component.styl']
})
export class IntroRolesComponent implements OnInit, AfterViewInit {

  constructor(
    private textService: TextService,
    private textHelperService: TextHelperService,
    private router: Router,
    private utils: Utils,
    private cdRef: ChangeDetectorRef,
    private locationService: LocationService
  ) { }

  roleData: any;
  activeIndex = 0;
  previousIndex = 0;
  activeRoleTitle;
  activeRoleData;
  showCarousel: Boolean = true;
  svgPath='';
  hideQuizBtn: Boolean;
  public showArrowIcon:Boolean = true;

  @ViewChildren('roleImage') roleImagesRef: QueryList<ElementRef>;
  @ViewChild('svgPathRef', {static: false}) svgPathRef: ElementRef;
  @ViewChild('svgCircle', {static: false}) svgCircleRef: ElementRef;

  @HostListener('document:keydown.arrowup', ['$event'])
  @HostListener('document:keydown.arrowdown', ['$event'])
  @HostListener('document:keydown.arrowleft', ['$event'])
  @HostListener('document:keydown.arrowright', ['$event'])
  public handleKeyboardEvent(event) {
    switch (event.keyCode) {
      case 39:
      case 40:
        $('.roles-manager:focus').next().focus();
        event.preventDefault();
        event.stopPropagation();
        break;
      case 37:
      case 38:
        $('.roles-manager:focus').prev().focus();
        event.preventDefault();
        event.stopPropagation();
        break;
    }
  }

  @HostListener('window:resize', ['$event'])
  public handleWindowResize(event) {
    this.createSVG()
    this.animateCircle()
  }

  ngOnInit() {
    this.getData()

    this.hideQuizBtn = window.location.href.split('/')[4]=="dashboard" ? true : false;
  }

  ngAfterViewInit() {
    this.createSVG();
    // this.moveCircle(this.activeIndex)
    this.animateCircle()
  }

  createSVG(){
    this.svgPath = '';
    let roleImagesArray = this.roleImagesRef.toArray();
    for (let i = 0; i < roleImagesArray.length; i++) {
      let elm = roleImagesArray[i].nativeElement;
      if (i == 0) {
        this.svgPath = "M" + (elm.offsetLeft - elm.offsetWidth / 2) + "," + (elm.offsetTop - elm.offsetHeight / 2)
      } else {
        this.svgPath += " L" + (elm.offsetLeft - elm.offsetWidth / 2) + "," + (elm.offsetTop - elm.offsetHeight / 2)
      }
    }
    this.cdRef.detectChanges();
  }

  getData() {
    let data = this.textHelperService.getInitiatives('IntroRoles')
    let data1 = this.utils.dictToJson(data);
    this.roleData = Object.keys(data1).map((key) => data1[key]);
    this.activeRoleTitle = this.roleData[0].header;
    this.activeRoleData = this.roleData[0].narrative;
  }

  onPrev(){
    if(this.activeIndex == 0){
      return
    }
    this.previousIndex = this.activeIndex;
    this.activeIndex--;
    this.getRoleData();
  }

  onNext(){
    if(this.activeIndex == this.roleData.length - 1){
      return
    }
    this.previousIndex = this.activeIndex;
    this.activeIndex++;
    this.getRoleData();
  }

  setActiveIndex(index){
    if(this.activeIndex != index){
      this.previousIndex = this.activeIndex;
      this.activeIndex = index;
      this.getRoleData();
    }
  }

  getRoleData() {
    this.activeRoleTitle = this.roleData[this.activeIndex].header;
    this.activeRoleData = this.roleData[this.activeIndex].narrative;

    this.showCarousel = this.activeIndex < 5 ? true : false;
    // this.moveCircle(this.activeIndex)
    this.animateCircle();
  }

  quiz() {
    let url = '/intro/quiz';
    this.locationService.setLocation(url, 1)
    this.router.navigate([url]);
  }

  // moveCircle(index) {
  //   let diffObj = {
  //     1: 12.5,
  //     2: 17.5,
  //     3: 20,
  //     4: 22.5,
  //     5: 25,
  //     6: 0
  //   }
  //   var diff = diffObj[index+1];
  //   // var diff = 10 + 2.5*(index+1);
  //   var percent = diff + (index/this.roleImagesRef.length *100);
  //   // Length of path
  //   var pathLength = Math.floor(this.svgPathRef.nativeElement.getTotalLength());

  //   // Move obj element along path based on percentage of total length
  //   percent = (percent * pathLength) / 100 ;

  //   // Get x and y values at a certain point in the line
  //   var pt = this.svgPathRef.nativeElement.getPointAtLength(percent);
  //   pt.x = Math.round(pt.x);
  //   pt.y = Math.round(pt.y);

  //   if(index == this.roleImagesRef.length - 1){
  //     var pt = this.svgPathRef.nativeElement.getPointAtLength(pathLength);
  //     pt.x = Math.round(pt.x);
  //     pt.y = Math.round(pt.y);
  //     pt.x += 40;
  //     pt.y += 40;
  //   }

  //   this.svgCircleRef.nativeElement.style.webkitTransform = 'translate3d(' + pt.x + 'px,' + pt.y + 'px, 0)';
  // }

  animateCircle(){
    //getCurrentPercentage
    let currentPerc = this.getPercent(this.previousIndex);
    //getNewPercentage
    let newPerc = this.getPercent(this.activeIndex);
    let isLast = this.activeIndex == this.roleImagesRef.length - 1
    //move from current to new by increasing 1 every milli second
    var difference = Math.round(Math.abs(newPerc - currentPerc));
    let direction = newPerc > currentPerc ? 1 : -1; 
    this.customLoop(currentPerc, difference, direction, isLast)
  }

  customLoop(currentPerc, difference, direction, isLast = false){
    let movePerc = currentPerc + direction;
    setTimeout(() => {
      this.moveObj(movePerc)
      if(difference == 0){
        if(isLast){
            var pt = this.svgPathRef.nativeElement.getPointAtLength(movePerc);
            pt.x = Math.round(pt.x);
            pt.y = Math.round(pt.y);
            pt.x += 40;
            pt.y += 40;
            this.svgCircleRef.nativeElement.style.webkitTransform = 'translate3d(' + pt.x + 'px,' + pt.y + 'px, 0)';
        }else{
          return
        }
      }else{
        this.customLoop(movePerc, difference - 1, direction, isLast)
      }
    }, 1);
  }

  moveObj(percent){
    var pt = this.svgPathRef.nativeElement.getPointAtLength(percent);
    pt.x = Math.round(pt.x);
    pt.y = Math.round(pt.y);
    this.svgCircleRef.nativeElement.style.webkitTransform = 'translate3d(' + pt.x + 'px,' + pt.y + 'px, 0)';
  }

  getPercent(index){
    let diffObj = {
      1: 12.5,
      2: 17.5,
      3: 20,
      4: 22.5,
      5: 25,
      6:30
    }
    var diff = diffObj[index+1];
    // var diff = 10 + 2.5*(index+1);
    var percent = diff + (index/this.roleImagesRef.length *100);
    // Length of path
    var pathLength = Math.floor(this.svgPathRef.nativeElement.getTotalLength());

    // Move obj element along path based on percentage of total length
    percent = (percent * pathLength) / 100 ;
    return percent;
  }

}
