import { Component, OnInit, OnDestroy } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs';
import { AppConstants } from 'src/app/app-constants';

const MAX_REGION = 2;
const MAX_PRODUCT = 2;
const MAX_PERIOD = 4;

@Component({
  selector: 'app-order-remaining',
  templateUrl: './order-remaining.component.html',
  styleUrls: ['./order-remaining.component.styl']
})
export class OrderRemainingComponent implements OnInit, OnDestroy {
  currentRound;
  currentPeriod;
  subscription: Subscription;
  remainingItemsList: Array<ORDER> = []
  planningSubmitted = false;

  constructor(
    private calcService: CalcService
  ) { }

  ngOnInit() {
    this.currentRound = +this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND)
    this.currentPeriod = +this.calcService.getValueForYear(AppConstants.REFS.CURRENT_PERIOD, AppConstants.REFS.CURRENT_ROUND)
    this.planningSubmitted = +this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) == 1

    this.initializeList();

    this.subscription = this.calcService.getObservable().subscribe(()=>{
      this.planningSubmitted = +this.calcService.getValueForYear(AppConstants.REFS.PLANNING_COMPLETED, AppConstants.REFS.CURRENT_ROUND) == 1
      
      let currentRound = +this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND)
      let currentPeriod = +this.calcService.getValueForYear(AppConstants.REFS.CURRENT_PERIOD, AppConstants.REFS.CURRENT_ROUND)
      if(currentRound != this.currentRound || currentPeriod != this.currentPeriod){
        this.currentRound = currentRound;
        this.currentPeriod = currentPeriod;

        //Reinitialize list if period or round changes
        this.initializeList()
      }else{
        this.updateList();
      }
    })
  }

  ngOnDestroy(){
    if(this.subscription){
      this.subscription.unsubscribe()
    }
  }

  initializeList(){
    this.remainingItemsList = [];

    // Since we need to filter out items only for current round, starting i as current round. Alternatively, we can remove the first for loop
    for(let round = this.currentRound; round <= this.currentRound; round++){
      for(let period = 1, periodThreshold = round == this.currentRound ? this.currentPeriod: MAX_PERIOD; period <= periodThreshold; period++){
        for(let region = 1; region <= MAX_REGION; region++){
          for(let product = 1; product <= MAX_PRODUCT; product++){
            let obj: ORDER = {
              ImageName: '',
              CustomerNameRange: 'tlOutput_Prod'+product+'_Reg'+region+'_P'+period+'_Order_Name_R'+round,
              CustomerName: '',
              ActualOrderRange: 'tlOutput_Prod'+product+'_Reg'+region+'_P'+period+'_Order_R'+round,
              ActualOrder: '',
              FullfilledOrderRange: 'tlOutput_Prod'+product+'_Reg'+region+'_P'+period+'_Order_Fulfilled_R'+round,
              FullfilledOrder: '',
              RemainingOrderRange: 'tlOutput_Prod'+product+'_Reg'+region+'_P'+period+'_Order_Remaining_R'+round,
              RemainingOrder: '',
              StatusRange: 'tlOutput_Prod'+product+'_Reg'+region+'_P'+period+'_Order_Check_R'+round,
              Status: '',
              ProductName: 'Prod'+product+'Name',
              RegionName: 'Reg'+region+'Name'
            }

            obj.ImageName = product == 1 ? 'prod-Betia.svg' : 'prod-GrowerBooster.svg'
            obj.CustomerName = this.calcService.getValue(obj.CustomerNameRange)
            obj.ActualOrder = this.calcService.getValue(obj.ActualOrderRange)
            obj.FullfilledOrder = this.calcService.getValue(obj.FullfilledOrderRange)
            obj.RemainingOrder = this.calcService.getValue(obj.RemainingOrderRange)
            obj.Status = +this.calcService.getValue(obj.StatusRange)

            // 2 = order fullfilled
            if(obj.Status != 2){
              this.remainingItemsList.push(obj)
            }
          }
        }
      }
    }

    console.log("Remaining Order List:",this.remainingItemsList)
  }

  updateList(){
    let currentList = [...this.remainingItemsList]

    // Use reverse for as index will change after item is removed
    for(let i=currentList.length - 1; i >= 0; i--){
      const item = currentList[i];
      item.Status = this.calcService.getValue(item.StatusRange)

      if(item.Status == 2){
        // remove element from list if order is fullfilled;
        this.remainingItemsList.splice(i,1)
      }
    }
  }

}


interface ORDER{
  ImageName,
  CustomerNameRange,
  CustomerName,
  ActualOrderRange,
  ActualOrder,
  FullfilledOrderRange,
  FullfilledOrder,
  RemainingOrderRange,
  RemainingOrder,
  StatusRange,
  Status,
  ProductName,
  RegionName
}