import { Component, OnInit, Input } from '@angular/core';
import { CalcService } from '@btsdigital/ngx-isomer-core';
import { debounceTime } from 'rxjs/operators';
import { AppConstants } from 'src/app/app-constants';
import { Subscription } from 'rxjs';

@Component({
  selector: 'planning-production',
  templateUrl: './planning-production.component.html',
  styleUrls: ['./planning-production.component.styl']
})
export class PlanningProductionComponent implements OnInit {
  @Input() isDisable:Boolean= false;
  isLocked = false;

  commonChartOptions = {
    title: {
      text: ''
    },
    chart: { 
      // 'height': 300, 
      // 'width': 885, 
      'type': 'column' 
    },
    legend: {
      enabled: true,
      x: 0,
      y: 5,
      reversed: false,
      verticalAlign: 'top',
      floating: true,
      align: 'right'
    },
    yAxis: {
      title: {
        text: ''
      },
      labels: { format: '{value:0}' },
      stackLabels: {
        enabled: false,
        format: '{1:0}',
        style: {
          // fontWeight: 'bold',
          display: 'none'
        }
      }
    },
    plotOptions:
    {
      column:
      {
        dataLabels: {
          enabled: false,
          format: '{y:0}',
          crop: false,
          overflow: true,
          inside: false
        },
      }
    },
    tooltip: {
      pointFormat: '{series.name}:{point.y: 0}'
    },
  }
  subscriber: Subscription;
  renderChart = false;

  constructor(
    private calcService: CalcService
  ) { }

  ngOnInit() {
    // this.checkIfLocked()
    // this.subscriber = this.calcService.getObservable().pipe(debounceTime(AppConstants.CALC_DEBOUNCE_TIME)).subscribe(()=>{
    //   this.checkIfLocked()
    // })
  }

  ngAfterViewInit(){
    setTimeout(() => {
      // Adding delay as there is animation
      this.renderChart = true;
    }, 1000);
  }

  ngOnDestroy(){
    if(this.subscriber){
      this.subscriber.unsubscribe()
    }
  }

  checkIfLocked(){
    this.isLocked = +this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND) == 1;
  }

}
