import { Component, HostListener, OnDestroy, OnInit, ViewChild, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { CommonModalComponent } from 'src/app/shared/common-modal/common-modal.component';
import { TextHelperService } from 'src/app/shared/text-service.extended';
import * as Collections from 'typescript-collections';
import { StorageService } from '../../../shared/storage.service';
import { Utils } from '../../../shared/utils/utils';
import { LocationService } from 'src/app/shared/location.service';

@Component({
    selector: 'feedback',
    templateUrl: './feedback.html',
    styleUrls: ['./feedback.styl'],
    host: {
        '[style.display]': "'block'"
    }
})
export class FeedbackComponent implements OnInit, OnDestroy {
    public id: string;
    scene: Collections.Dictionary<string, string>;
    choice: number = -1;
    public feedback: string = "";
    public choices = [];
    public tabVal: any;
    @Output() onCloseClick = new EventEmitter()
    // @ViewChild('feedbackModal', {static: true}) childModal: CommonModalComponent;

    // @HostListener('document:keydown.arrowup', ['$event'])
    // @HostListener('document:keydown.arrowdown', ['$event'])
    // @HostListener('document:keydown.arrowleft', ['$event'])
    // @HostListener('document:keydown.arrowright', ['$event'])
    // public handleKeyboardEvent(event) {
    //     switch (event.keyCode) {
    //         case 39:
    //         case 40:
    //             $('.nav-item:focus').next().focus();
    //             event.preventDefault();
    //             event.stopPropagation();
    //             break;
    //         case 37:
    //         case 38:
    //             $('.nav-item:focus').prev().focus();
    //             event.preventDefault();
    //             event.stopPropagation();
    //             break;
    //     }
    // }
    constructor(private calcService: CalcService, private textEngineService: TextService, private activatedRoute: ActivatedRoute, 
        private router: Router, private utils: Utils, private storageService: StorageService,
        private textHelper: TextHelperService, private locationService: LocationService
    ) { }

    ngOnInit() {
        this.activatedRoute.params.forEach(params => this.id = params['id']);
        this.scene = this.textEngineService.getScene(this.id);

        // get choice made
        var curVal = this.calcService.getValue("tlInput" + this.scene.getValue("ID"));
        if (curVal != "0") {
            // Answer is stored as A,B,C in model where as used as 1,2,3,... in html/ts
            this.choice = curVal.charCodeAt(0) - 64;
        }
        this.tabVal = this.choice;
        
        this.choices = this.textHelper.getSingleSelectChoices(this.id);
        // set feedback text
        this.feedback = this.scene.getValue("alt" + this.choice + "_feedback");
    }

    ngOnDestroy() {

    }

    onClose() {
        if(this.calcService.getValue("tlInput_READ_" +this.id) != '1'){
            this.calcService.setValue("tlInput_READ_" +this.id, 1);
        }
        // setTimeout(() => {
        //     this.locationService.setLocationAsCurrentURL(1)
        // }, 100);
        // this.router.navigate(['.', {outlets: {'secondary-nav': null}}], { relativeTo: this.activatedRoute.parent })
        this.onCloseClick.emit()
    }
}
